import axios from "axios";
import AxiosInstance from "../utils/AxiosInstance";
import {
  getAutenticarUrl,
  getAutorizarUrl,
  getRegistrarUrl,
  getRecuperarPasswordUrl,
  getChangePasswordUrl,
  getRegistrarPersonaUrl,
} from "../utils/urls";

const config = {
  headers: {
    "Accept-Language": "en_US",
  },
};

export default class CuentaService {
  autenticar = (props) => {
    const { credenciales } = props;
    return axios.create(config).post(getAutenticarUrl(), credenciales);
  };
  autorizar = (props) => {
    const { credenciales } = props;
    return axios.create(config).post(getAutorizarUrl(), credenciales);
  };
  register = (props) => {
    const { datos } = props;
    return axios.create(config).post(getRegistrarUrl(), datos);
  };
  registerPersona = (props) => {
    const { datos } = props;
    return axios.create(config).post(getRegistrarPersonaUrl(), datos);
  };
  recoverPassword = (props) => {
    const { datos } = props;
    return axios.post(getRecuperarPasswordUrl(), datos);
  };
  resetPassword = (props) => {
    const { datos } = props;
    return axios.post(getRecuperarPasswordUrl(), datos);
  };
  changePassword = (props) => {
    const { navigate, datos } = props;
    return AxiosInstance(navigate).post(getChangePasswordUrl(), datos);
  };
}
