import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import {
  Input,
  Form,
  Button,
  Row,
  Col,
  Card,
  Image,
  Typography,
  Divider,
} from "antd";
import AlertWait from "./AlertWait";
import Logo from "./../components/Logo";
import md5 from "md5";
import CuentaService from "../service/CuentaService";
import { getDatosUsuario } from "../utils/Utils";

import "./main.css";

const cuentaService = new CuentaService();

const Signin = () => {
  const history = useHistory();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState("");
  const [idUsuario, setIdUsuario] = useState("");
  const [perfiles, setPerfiles] = useState([]);
  const [form] = Form.useForm();

  const autorizar = (credenciales) => {
    setError("");
    setIsLoading(true);
    cuentaService
      .autorizar({ credenciales })
      .then((response) => {
        setIsLoading(false);
        const { data, headers } = response;
        const { authorization } = headers;
        if (authorization) {
          localStorage.setItem(
            "ckToken",
            authorization.replace("Bearer", "").trim()
          );
        }
        localStorage.setItem(
          "ckDatosUsuario",
          JSON.stringify({ ...getDatosUsuario(), ...data.listObject[0] })
        );
        history.push("/");
        history.push(getDatosUsuario().opciones[0].actionOpcion || "/");
      })
      .catch((error) => {
        setIsLoading(false);
        const { response } = error;
        if (response.status === 500) {
          setError(response.statusText);
        } else if (response.status === 404) {
          setError(
            "El recurso solicitado no esta disponible o no se encuentra"
          );
        } else {
          setError(response.data[0].messageText);
        }
      });
  };

  const login = (values) => {
    setError("");
    const credenciales = { ...values, password: md5(values.password) };
    setIsLoading(true);
    cuentaService
      .autenticar({ credenciales })
      .then((response) => {
        setIsLoading(false);
        const { data } = response;
        const usuario = data.listObject[0];
        localStorage.setItem("ckDatosUsuario", JSON.stringify(usuario));
        setIdUsuario(data.listObject[0].usuarioId);
        if (
          data.listObject[0].perfiles &&
          data.listObject[0].perfiles.length > 0
        ) {
          if (data.listObject[0].perfiles.length === 1) {
            const credenciales = {
              usuId: data.listObject[0].usuarioId,
              perId: data.listObject[0].perfiles[0].perId,
            };
            autorizar(credenciales);
          } else if (data.listObject[0].perfiles.length > 1) {
            const { perfiles } = data.listObject[0];
            setPerfiles(perfiles);
          }
        }
      })
      .catch((error) => {
        setIsLoading(false);
        const { response } = error;
        if (response.status === 500) {
          setError(response.statusText);
        } else if (response.status === 404) {
          setError(
            "El recurso solicitado no esta disponible o no se encuentra"
          );
        } else {
          setError(response.data[0].messageText);
        }
      });
  };

  const registrar = () => {
    history.push("/register");
  };

  const recuperarPassword = () => {
    history.push("/recover");
  };

  const renderPerfil = (p) => {
    const { idPerfil, perfil } = p;
    return (
      <Col key={idPerfil} xs={12} sm={12} md={12} lg={12} xl={12}>
        <Card
          hoverable
          onClick={() => autorizar({ usuId: idUsuario, perId: idPerfil })}
          actions={[
            <Typography.Title level={5} style={{ color: "#333" }}>
              {perfil}
            </Typography.Title>,
          ]}
          style={{
            backgroundColor: "#F0F0F0",
            borderColor: "#CCC",
          }}
        >
          <Row justify="center">
            <Col>
              <Image src={"./assets/" + perfil + "_1.png"} preview={false} />
            </Col>
          </Row>
        </Card>
      </Col>
    );
  };

  return (
    <>
      <div className="wrap-login100">
        <div
          className="login100-form validate-form"
          style={{
            height: "100vh",
          }}
        >
          <Row>
            <Col span={24}>
              <Row justify="center">
                <Col xs={24} sm={22} md={20} lg={20} xl={18} xxl={16}>
                  <Logo />
                </Col>
              </Row>
              {perfiles.length > 0 ? (
                <Row gutter={[10, 10]}>
                  <Col span={24}>
                    <Divider>
                      <span
                        style={{
                          textTransform: "uppercase",
                          letterSpacing: "1px",
                        }}
                      >
                        Ingresar como
                      </span>
                    </Divider>
                  </Col>
                  {perfiles.length > 0 && perfiles.map((p) => renderPerfil(p))}
                </Row>
              ) : (
                <Row>
                  <Col span={24}>
                    <Form layout={"vertical"} form={form} onFinish={login}>
                      <Form.Item
                        label="Usuario"
                        name="login"
                        rules={[
                          {
                            required: true,
                            message: "Ingrese el Usuario",
                          },
                        ]}
                      >
                        <Input type="text" />
                      </Form.Item>
                      <Form.Item
                        label="Contraseña"
                        name="password"
                        rules={[
                          {
                            required: true,
                            message: "Ingrese la contraseña",
                          },
                        ]}
                      >
                        <Input.Password />
                      </Form.Item>
                      <Form.Item>
                        <Button
                          type="primary"
                          htmlType="submit"
                          loading={isLoading}
                          disabled={isLoading}
                          block
                        >
                          Ingresar
                        </Button>
                      </Form.Item>
                    </Form>
                  </Col>
                </Row>
              )}
              <AlertWait error={error} isLoading={isLoading} />
              <Row justify="center" style={{ paddingTop: "15px" }}>
                <Col>
                  <Button type="link" onClick={recuperarPassword}>
                    Olvide mi contraseña
                  </Button>
                </Col>
              </Row>
              <Row justify="center" style={{ paddingTop: "15px" }}>
                <Col>
                  ¿No tienes cuenta?{" "}
                  <Button type="link" onClick={registrar}>
                    Regístrate aquí
                  </Button>
                </Col>
              </Row>
            </Col>
          </Row>
        </div>
        <div
          className="login100-more"
          style={{
            backgroundImage: "url(./assets/loginBackground.svg)",
            borderRight: "solid 1px #999",
          }}
        ></div>
      </div>
    </>
  );
};

export default Signin;
