import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import {
  Input,
  Form,
  Button,
  Row,
  Col,
  Divider,
  Checkbox,
  Select,
} from "antd";
import PasswordChecklist from "react-password-checklist";
import AlertWait from "./AlertWait";
import Logo from "../components/Logo";
import md5 from "md5";
import CuentaService from "./../service/CuentaService";
import CatalogoService from "./../service/CatalogoService";
import ResultRegistro from "../components/registro/ResultRegistro";

const cuentaService = new CuentaService();
const catalogoService = new CatalogoService();
const formItemLayout = { xs: 24, sm: 12, md: 12, lg: 12, xl: 12 };
const { Option } = Select;

const RegisterPYME = () => {
  const navigate = useHistory();
  const [load, setLoad] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [isRegistered, setIsRegistered] = useState(false);
  const [error, setError] = useState(null);

  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");

  const [tiposPersona, setTiposPersona] = useState([]);
  const [tiposIdentificacion, setTiposIdentificacion] = useState([]);
  const [actividades, setActividades] = useState([]);
  const [facturacion, setFacturacion] = useState([]);

  const renderError = (e) => <div key={e.messageCode}>{e.messageText}</div>;
  const renderCatalogo = (e) => (
    <Option key={e.catId} value={e.catId}>
      {e.catNombre}
    </Option>
  );

  const login = () => {
    navigate.push("/login");
  };
  const registrarInversionista = () => {
    navigate.push("/register");
  };

  const registrar = async (values) => {
    setError(null);
    setIsSaving(true);
    const nuevaPersona = { ...values, perPassword: md5(values.perPassword) };
    try {
      const { data } = await cuentaService.registerPersona({
        datos: { ...nuevaPersona },
      });
      if (data && data.listObject.length > 0 && data.listObject[0].perId) {
        setIsRegistered(true);
        // message.success('Información registrada correctamente')
        // navigate.push('/login')
      }
    } catch (error) {
      const { status, data } = error.response;
      switch (status) {
        case 409:
          setError(data.map((e) => renderError(e)));
          break;
        case 500:
          setError(data);
          break;
        default:
          break;
      }
    } finally {
      setIsSaving(false);
    }
  };

  const fetchData = async () => {
    setLoad(false);
    setIsLoading(true);
    try {
      const responses = await Promise.all([
        catalogoService.getTiposIdentificacion(navigate),
        catalogoService.getTiposPersona(navigate),
        catalogoService.getActividadesEconomicas(navigate),
        catalogoService.getFacturacionMensual(navigate),
      ]);
      if (responses) {
        setTiposIdentificacion(responses[0].data.listObject);
        setTiposPersona(responses[1].data.listObject);
        setActividades(responses[2].data.listObject);
        setFacturacion(responses[3].data.listObject);
      }
    } catch (e) {
      console.log(e);
      setError("Ocurrio en error al consultar los catálogos");
      // const { response } = error
      // setError(response.status === 409 ? response.data[0].messageText : "No pudimos hacer la solicitud para obtener el catálogo")
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    if (load) fetchData();
  });

  return (
    <Row justify="center" style={{ backgroundColor: "#F0F0F0" }}>
      <Col xs={22} sm={22} md={18} lg={12} xl={12}>
        <Col span={24}>
          <Row style={{ textAlign: "center" }}>
            <Col span={18} offset={3}>
              <Logo />
            </Col>
          </Row>
        </Col>
        {isRegistered ? (
          <ResultRegistro />
        ) : (
          <>
            <Divider>
              <span
                style={{ textTransform: "uppercase", letterSpacing: "1px" }}
              >
                Registrarme como
              </span>
            </Divider>
            <Row gutter={[10, 10]} style={{ paddingBottom: "20px" }}>
              <Col span={12}>
                <Button
                  type="default"
                  onClick={(e) => registrarInversionista()}
                  block
                >
                  INVERSIONISTA
                </Button>
              </Col>
              <Col span={12}>
                <Button type="primary" block>
                  PERSONA
                </Button>
              </Col>
            </Row>
            {isLoading ? (
              <AlertWait
                tipo="warning"
                error={error}
                mensaje={<>Espere un momento...</>}
                isLoading={isLoading}
              />
            ) : (
              <Form
                layout={"vertical"}
                initialValues={{
                  perTerCon: false,
                }}
                onFinish={registrar}
                disabled={isSaving}
              >
                <Row gutter={[10, 10]}>
                  <Col span={24}>
                    <Form.Item
                      label="Tipo de Persona"
                      name="perIdTipPer"
                      rules={[
                        {
                          required: true,
                          message: "Seleccione el tipo de persona",
                        },
                      ]}
                    >
                      <Select>
                        {tiposPersona &&
                          tiposPersona.map((p) => renderCatalogo(p))}
                      </Select>
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={[10, 10]}>
                  <Col {...formItemLayout}>
                    <Form.Item
                      label="Tipo de Identificación"
                      name="perIdTipoDoc"
                      rules={[
                        {
                          required: true,
                          message: "Selecccione el tipo de identificación",
                        },
                      ]}
                    >
                      <Select>
                        {tiposIdentificacion &&
                          tiposIdentificacion.map((p) => renderCatalogo(p))}
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col {...formItemLayout}>
                    <Form.Item
                      label="Identificación"
                      name="perDocumento"
                      rules={[
                        {
                          required: true,
                          message: "Ingrese su identificación",
                        },
                      ]}
                    >
                      <Input type="text" />
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={[10, 10]}>
                  <Col span={24}>
                    <Form.Item
                      label="Razón Social"
                      name="perRazonSocial"
                      rules={[
                        {
                          required: true,
                          message: "Ingrese la razón social",
                        },
                      ]}
                    >
                      <Input type="text" />
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={[10, 10]}>
                  <Col {...formItemLayout}>
                    <Form.Item
                      label="Tipo de Actividad"
                      name="perIdActividad"
                      rules={[
                        {
                          required: true,
                          message: "Seleccione el tipo de actividad",
                        },
                      ]}
                    >
                      <Select>
                        {actividades &&
                          actividades.map((p) => renderCatalogo(p))}
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col {...formItemLayout}>
                    <Form.Item
                      label="N° Clientes"
                      name="perClientes"
                      rules={[
                        {
                          required: true,
                          message: "Ingrese el número de clientes",
                        },
                      ]}
                    >
                      <Input type="number" />
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={[10, 10]}>
                  <Col span={24}>
                    <Form.Item
                      label="¿Cuanto factura tu negocio al mes?"
                      name="perFacturacionMensual"
                      rules={[
                        {
                          required: true,
                          message: "¿Cuanto factura tu negocio al mes?",
                        },
                      ]}
                    >
                      <Select>
                        {facturacion &&
                          facturacion.map((p) => renderCatalogo(p))}
                      </Select>
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={[10, 10]}>
                  <Col span={24}>
                    <Form.Item
                      label="Dirección"
                      name="perDireccion"
                      rules={[
                        {
                          required: true,
                          message: "Ingrese la dirección",
                        },
                      ]}
                    >
                      <Input type="text" />
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={[10, 10]}>
                  <Col {...formItemLayout}>
                    <Form.Item
                      label="Teléfono"
                      name="perTelefono"
                      rules={[
                        {
                          required: true,
                          message: "Ingrese el teléfono",
                        },
                      ]}
                    >
                      <Input type="text" />
                    </Form.Item>
                  </Col>
                  <Col {...formItemLayout}>
                    <Form.Item
                      label="Sitio WEB"
                      name="perSitioWeb"
                      rules={[
                        {
                          required: false,
                          type: "url",
                          message: "Ingrese el sitio web",
                        },
                      ]}
                    >
                      <Input type="text" />
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={[10, 10]}>
                  <Col span={24}>
                    <Form.Item
                      label="Descripción del Negocio"
                      name="perDescripcion"
                      rules={[
                        {
                          required: true,
                          message: "Ingrese la descripción del negocio",
                        },
                      ]}
                    >
                      <Input type="text" />
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={[10, 10]}>
                  <Col span={24}>
                    <Form.Item
                      label="¿Cuanto factura tu negocio al mes?"
                      name="perFacturaMes"
                      rules={[
                        {
                          required: true,
                          message: "¿Cuanto factura tu negocio al mes?",
                        },
                      ]}
                    >
                      <Input type="number" />
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={[10, 10]}>
                  <Col span={24}>
                    <Form.Item
                      label="Email"
                      name="perEmail"
                      rules={[
                        {
                          required: true,
                          type: "email",
                          message: "Ingrese el email",
                        },
                      ]}
                    >
                      <Input type="text" />
                    </Form.Item>
                  </Col>
                </Row>
                <Row gutter={[10, 10]}>
                  <Col {...formItemLayout}>
                    <Form.Item
                      label="Nombres"
                      name="perNombres"
                      rules={[
                        {
                          required: true,
                          message: "Ingrese sus nombres",
                        },
                      ]}
                    >
                      <Input type="text" />
                    </Form.Item>
                  </Col>
                  <Col {...formItemLayout}>
                    <Form.Item
                      label="Apellidos"
                      name="perApellidos"
                      rules={[
                        {
                          required: true,
                          message: "Ingrese sus apellidos",
                        },
                      ]}
                    >
                      <Input type="text" />
                    </Form.Item>
                  </Col>
                </Row>

                <Row gutter={[10, 10]}>
                  <Col {...formItemLayout}>
                    <Form.Item
                      label="Contraseña"
                      name="perPassword"
                      rules={[
                        {
                          required: true,
                          message: "Ingrese la contraseña",
                        },
                      ]}
                    >
                      <Input.Password
                        onChange={(e) => setPassword(e.target.value)}
                      />
                    </Form.Item>
                  </Col>
                  <Col {...formItemLayout}>
                    <Form.Item
                      label="Confirme la contraseña"
                      name="perPasswordConfirm"
                      rules={[
                        {
                          required: true,
                          message: "Confirme la contraseña",
                        },
                      ]}
                    >
                      <Input.Password
                        onChange={(e) => setPasswordConfirm(e.target.value)}
                      />
                    </Form.Item>
                  </Col>
                  <Col span={24}>
                    <PasswordChecklist
                      rules={[
                        "minLength",
                        "specialChar",
                        "number",
                        "capital",
                        "match",
                      ]}
                      minLength={8}
                      value={password}
                      valueAgain={passwordConfirm}
                      messages={{
                        minLength: "La contraseña tiene más de 8 caracteres.",
                        specialChar:
                          "La contraseña tiene caracteres especiales.",
                        number: "La contraseña tiene un número.",
                        capital: "La contraseña tiene una letra mayúscula.",
                        match: "Las contraseñas coinciden.",
                      }}
                      onChange={(isValid) => {}}
                    />
                  </Col>
                  <Col span={24}>
                    <Form.Item
                      name="perTerCon"
                      valuePropName="checked"
                      rules={[
                        {
                          validator: (_, value) =>
                            value
                              ? Promise.resolve()
                              : Promise.reject(
                                  new Error(
                                    "Acepte los términos y condiciones y el acuerdo de uso de la plataforma"
                                  )
                                ),
                        },
                      ]}
                    >
                      <Checkbox>
                        Estoy de acuerdo y acepto los{" "}
                        <a
                          rel="noreferrer"
                          href="https://www.koyntech.com/?page_id=1183"
                          target="_blank"
                        >
                          Términos y Condiciones
                        </a>{" "}
                        y el{" "}
                        <a
                          rel="noreferrer"
                          href="https://www.koyntech.com/?page_id=1179"
                          target={"_blank"}
                        >
                          Acuerdo de Uso
                        </a>{" "}
                        de la Plataforma KOYN que incluye la Pólitica de
                        Privacidad aplicable al tratamiento de la información de
                        datos personales.
                      </Checkbox>
                    </Form.Item>
                  </Col>
                </Row>
                <Form.Item>
                  <Button
                    type="primary"
                    htmlType="submit"
                    loading={isSaving}
                    disabled={isSaving}
                    block
                  >
                    {isSaving ? "Guardando..." : "Registrar"}
                  </Button>
                </Form.Item>
              </Form>
            )}
            {error && <AlertWait error={error} isLoading={isLoading} />}
            <Row
              justify="center"
              style={{ paddingTop: "15px", marginBottom: "25px" }}
            >
              <Col>
                ¿Ya estás registrado?{" "}
                <Button type="link" onClick={login}>
                  Ingresa aquí
                </Button>
              </Col>
            </Row>
          </>
        )}
      </Col>
    </Row>
  );
};

export default RegisterPYME;
