import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { message } from "antd";

import CatalogoService from "./../service/CatalogoService";
import PersonaService from "../service/PersonaService";

const catalogoService = new CatalogoService();
const personaService = new PersonaService();

const usePersona = (empresaInicial) => {
  const navigate = useHistory();
  const [empresa, setEmpresa] = useState(empresaInicial);
  const [load, setLoad] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);

  const [tiposPersona, setTiposPersona] = useState([]);
  const [tiposIdentificacion, setTiposIdentificacion] = useState([]);
  const [actividades, setActividades] = useState([]);

  const fetchData = async () => {
    setLoad(false);
    setIsLoading(true);
    const persona = { ...empresa };
    try {
      const responses = await Promise.all([
        catalogoService.getTiposIdentificacion(navigate),
        catalogoService.getTiposPersona(navigate),
        catalogoService.getActividadesEconomicas(navigate),
        personaService.get({ navigate, persona }),
      ]);
      if (responses) {
        setTiposIdentificacion(responses[0].data.listObject);
        setTiposPersona(responses[1].data.listObject.filter((t) => t !== null));
        setActividades(responses[2].data.listObject);
        setEmpresa(responses[3].data.listObject[0]);
      }
    } catch (e) {
      message.error("Ocurrio en error al consultar los catálogos");
      // const { response } = error
      // setError(response.status === 409 ? response.data[0].messageText : "No pudimos hacer la solicitud para obtener el catálogo")
    } finally {
      setIsLoading(false);
    }
  };

  const consultar = async () => {
    setIsLoading(true);
    const persona = { ...empresa };
    try {
      const { data } = await personaService.get({ navigate, persona });
      setEmpresa(data.listObject[0]);
      if (data.listObject.length <= 0)
        message.info("No se encontraron resultados");
    } catch ({ response }) {
      const { data } = response;
      message.error(
        response.status === 409
          ? data[0].messageText
          : "No fue posible consultar la empresa"
      );
    } finally {
      setIsLoading(false);
    }
  };

  const guardar = async () => {
    setIsSaving(true);
    setTimeout(() => {
      setIsSaving(false);
      message.success("Empresa guardada");
    }, 2000);
  };

  const eliminar = async () => {
    setIsDeleting(true);
    setTimeout(() => {
      setIsDeleting(false);
      message.success("Empresa eliminada");
    }, 2000);
  };

  useEffect(() => {
    if (load) fetchData();
  });

  return {
    empresa,
    isLoading,
    isSaving,
    isDeleting,
    tiposIdentificacion,
    tiposPersona,
    actividades,
    consultar,
    guardar,
    eliminar,
  };
};

export default usePersona;
