import React, { useEffect, useState } from "react";
import { useHistory, useParams, Link } from "react-router-dom";
import {
  Col,
  Row,
  Button,
  Result,
  Form,
  Input,
  Select,
  Spin,
  Typography,
  Divider,
  message,
  List,
} from "antd";
import * as anticons from "@ant-design/icons";
import UsuarioService from "../../service/UsuarioService";
import CatalogoService from "./../../service/CatalogoService";

const usuarioService = new UsuarioService();
const catalogoService = new CatalogoService();

const Usuario = () => {
  const navigate = useHistory();
  const { id } = useParams();
  const [isLoading, setIsLoading] = useState(true);
  const [isSaving, setIsSaving] = useState(false);
  const [estados, setEstados] = useState([]);
  const [tiposIdentificacion, setTiposIdentificacion] = useState([]);
  const [usuario, setUsuario] = useState({});
  const [error, setError] = useState(null);

  const [form] = Form.useForm();

  useEffect(() => {
    if (isLoading) {
      const fetchData = async () => {
        catalogoService
          .getPorPadre({ navigate, catalogo: { catValor: "CAT_ESTADOS" } })
          .then((r) => {
            const { data } = r;
            setEstados(data.listObject);
          });

        catalogoService.getTiposIdentificacion(navigate).then((r) => {
          const { data } = r;
          setTiposIdentificacion(data.listObject);
        });

        try {
          const { data } = await usuarioService.get({
            navigate,
            usuario: { usuId: id },
          });
          if (data) {
            setUsuario({ ...data.listObject[0], password: "" });
            setError(null);
            setIsLoading(false);
          } else {
            setIsLoading(false);
            setError("Hubo un error al obtener el usuario");
          }
        } catch (error) {
          setIsLoading(false);
          const { response } = error;
          setError(
            response.status === 409
              ? response.data[0].messageText
              : "No pudimos hacer la solicitud para obtener el usuario"
          );
        }
      };
      fetchData();
    }
  }, [isLoading, navigate, id]);

  const guardar = async (values) => {
    setError("");
    setIsSaving(true);
    try {
      const { data } = await usuarioService.set({
        navigate,
        usuario: {
          ...values,
          cultura: "en_US",
          tipoDocId: values.tipoDocumento.catId,
          estadoId: values.estado.catId,
        },
      });
      setIsSaving(false);
      if (data) message.success("Usuario guardado correctamente");
      const usuarioActualizado = data.listObject[0];
      setUsuario(usuarioActualizado);
    } catch (error) {
      setIsSaving(false);
      setError("No pudimos hacer la solicitud para guardar el usuario");
    }
  };

  if (error) {
    return (
      <Result
        status="warning"
        title={""}
        subTitle={""}
        extra={
          <>
            <h5>{error}</h5>
            <Link to={"/usuarios"}>Regresar al inicio</Link>
          </>
        }
      />
    );
  }

  return (
    <>
      <Row align="middle" gutter={10}>
        <Col>
          <Link to={"/usuarios"}>
            <anticons.ArrowLeftOutlined />
          </Link>
        </Col>
        <Col>
          <Typography.Title
            level={4}
            style={{
              margin: 0,
            }}
          >
            Gestión de Usuario
          </Typography.Title>
        </Col>
      </Row>
      <Divider></Divider>
      {isLoading ? (
        <div className="p-10 text-center">
          <Spin size="large" />
          <br />
          <Typography.Text>Espere un momento...</Typography.Text>
        </div>
      ) : (
        <Form
          layout={"vertical"}
          form={form}
          initialValues={usuario}
          onFinish={guardar}
        >
          <Row gutter={10}>
            <Col span={24} style={{ display: "none" }}>
              <Form.Item name="usuId" label="ID">
                <Input />
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
              <Form.Item name="documento" label="Identificación">
                <Input />
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
              <Form.Item
                name={["tipoDocumento", "catId"]}
                label="Tipo de Identificación"
              >
                <Select
                  fieldNames={{
                    label: "catNombre",
                    value: "catId",
                  }}
                  showSearch
                  options={tiposIdentificacion}
                  optionFilterProp="catNombre"
                />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={10}>
            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
              <Form.Item name="nombres" label="Nombres">
                <Input />
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
              <Form.Item name="apellidos" label="Apellidos">
                <Input />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={10}>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              <Form.Item name="direccion" label="Dirección">
                <Input />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={10}>
            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
              <Form.Item label="Email" name="email">
                <Input />
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
              <Form.Item label="Teléfono" name="telefono">
                <Input />
              </Form.Item>
            </Col>
          </Row>

          <Row gutter={10}>
            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
              <Form.Item name={["estado", "catId"]} label="Estado">
                <Select
                  fieldNames={{
                    label: "catNombre",
                    value: "catId",
                  }}
                  showSearch
                  options={estados}
                  optionFilterProp="catNombre"
                />
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
              <Form.Item label="Perfiles">
                <List
                  bordered={false}
                  size='small'                  
                  dataSource={usuario.perfiles.map((e) => e.perNombre)}
                  renderItem={(item) => (
                    <List.Item>
                      {item}
                    </List.Item>
                  )}
                />
              </Form.Item>
            </Col>
          </Row>
          <Form.Item>
            <Button
              type="primary"
              disabled={isSaving}
              loading={isSaving}
              onClick={() => form.submit()}
            >
              {isSaving ? "Guardando..." : "Guardar"}
            </Button>
          </Form.Item>
        </Form>
      )}
    </>
  );
};

export default Usuario;
