import React, { useState, useEffect } from "react";
import { useHistory, Link } from "react-router-dom";
import {
  Typography,
  Button,
  Table,
  Row,
  Col,
  Divider,
  Input,
  Radio,
  Form,
  message,
} from "antd";
import * as anticons from "@ant-design/icons";
import UsuarioService from "../../service/UsuarioService";

const usuarioService = new UsuarioService();

const tableProps = {
  bordered: false,
  loading: false,
  title: "",
  showHeader: true,
  size: "medium",
  pagination: {
    position: ["none", "bottomRight"],
  },
};

const columnas = [
  {
    title: "Nombre",
    dataIndex: "unique",
    render: (e) => <Link to={`/usuario/${e.key}`}>{e.label}</Link>,
  },
  {
    title: "Identificación",
    dataIndex: "documento",
  },
  {
    title: "Email",
    dataIndex: "email",
    responsive: ["xl"],
  },
  {
    title: "Estado",
    dataIndex: "estado",
    render: (text) => text.catNombre,
  },
];

const Usuarios = () => {
  const navigate = useHistory();
  const [load, setLoad] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [usuarios, setUsuarios] = useState([]);
  // const [error, setError] = useState(null);

  const [documento, setDocumento] = useState(null);
  const [email, setEmail] = useState(null);
  const [nombres, setNombres] = useState(null);
  const [estado, setEstado] = useState(null);

  const fetchData = async () => {
    setLoad(false);
    setIsLoading(true);
    setUsuarios([]);
    try {
      let filtros = {};
      if (documento) {
        filtros = { ...filtros, documento };
      }
      if (email) {
        filtros = { ...filtros, email };
      }
      if (nombres) {
        filtros = { ...filtros, nombres };
      }
      if (estado !== null) {
        filtros = { ...filtros, estadoId: estado };
      }
      const { data } = await usuarioService.getsFiltro({
        navigate,
        filtros,
      });
      if (data) {
        const _usuarios = data.listObject.map((u) => ({
          ...u,
          unique: {
            key: u.usuId,
            label: `${u.nombres} ${u.apellidos || ""}`,
          },
        }));
        setUsuarios(_usuarios);
      } else {
        message.warning("Hubo un error al obtener los usuarios.");
      }
    } catch (error) {
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para obtener los usuarios."
      );
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    if (load) {
      fetchData();
    }
  });

  // if (error) {
  //   return (
  //     <Result
  //       status="warning"
  //       title={""}
  //       subTitle={""}
  //       extra={
  //         <>
  //           <h5>{error}</h5>
  //           <Link to={"/"}>Regresar al inicio</Link>
  //         </>
  //       }
  //     />
  //   );
  // }

  return (
    <>
      <Row>
        <Col span={24}>
          <Row justify="start">
            <Typography.Title
              level={4}
              style={{
                margin: 0,
              }}
            >
              Gestión de Usuarios
            </Typography.Title>
          </Row>
        </Col>
      </Row>
      <Divider></Divider>
      <Form layout={"vertical"}>
        <Row gutter={10}>
          <Col xs={24} sm={12} md={12} lg={6} xl={6}>
            <Form.Item label="Identificación">
              <Input
                label="Identificación"
                type="text"
                value={documento}
                onChange={(e) => setDocumento(e.target.value)}
              />
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={12} lg={6} xl={6}>
            <Form.Item label="Nombres">
              <Input
                label="Nombres"
                type="text"
                value={nombres}
                onChange={(e) => setNombres(e.target.value)}
              />
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={12} lg={6} xl={6}>
            <Form.Item label="Email">
              <Input
                label="Email"
                type="text"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={12} lg={6} xl={6}>
            <Form.Item label="Estado">
              <Radio.Group
                onChange={(e) => setEstado(e.target.value)}
                value={estado}
              >
                <Radio value={70}>Activo</Radio>
                <Radio value={71}>Inactivo</Radio>
              </Radio.Group>
            </Form.Item>
          </Col>
        </Row>
        <Row justify="center">
          <Col>
            <Form.Item>
              <Button
                type="primary"
                onClick={fetchData}
                icon={<anticons.SearchOutlined />}
                loading={isLoading}
                disabled={isLoading}
              >
                Consultar
              </Button>
            </Form.Item>
          </Col>
        </Row>
      </Form>
      <Row justify="end">
        <Col>
          <Typography.Paragraph strong>
            {`${usuarios && usuarios.length.toString()} registros encontrados`}
          </Typography.Paragraph>
        </Col>
      </Row>
      <Table
        {...tableProps}
        rowKey="usuId"
        columns={columnas}
        dataSource={usuarios}
        loading={isLoading}
      />
    </>
  );
};

export default Usuarios;
