import AxiosInstance from "../utils/AxiosInstance";
import {
  getActualizarPersonaUrl,
  getPersonaByIdUrl,
  getPersonaListUrl,
} from "../utils/urls";

export class PersonaService {
  get = (props) => {
    const { navigate, persona } = props;
    return AxiosInstance(navigate).post(getPersonaByIdUrl(), persona);
  };
  gets = (props) => {
    const { navigate } = props;
    return AxiosInstance(navigate).get(getPersonaListUrl());
  };
  set = (props) => {
    const { navigate, persona } = props;
    return AxiosInstance(navigate).post(getActualizarPersonaUrl(), persona);
  };
}

export default PersonaService;
