import axios from "axios";
import AxiosInstance from "./../utils/AxiosInstance";
import {
  getActualizarCatalogoUrl,
  getCatalogosFilterUrl,
  getCatalogosPorIdUrl,
  getCatalogosPorPadreUrl,
  getCatalogosUrl,
  getEliminarCatalogoUrl,
  getInsertarCatalogoUrl,
} from "./../utils/urls";
export default class CatalogoService {
  getTiposIdentificacion = () =>
    this.getPorPadre({ catalogo: { catValor: "CAT_TIP_IDE" } });
  getTiposPersona = () =>
    this.getPorPadre({ catalogo: { catValor: "CAT_TIP_PER" } });
  getActividadesEconomicas = () =>
    this.getPorPadre({ catalogo: { catValor: "CAT_ACT_ECO" } });
  getBancos = () => this.getPorPadre({ catalogo: { catValor: "CAT_INS_FIN" } });
  getTiposCuenta = () =>
    this.getPorPadre({ catalogo: { catValor: "CAT_TIP_CUE" } });
  getFuentesIngreso = () =>
    this.getPorPadre({ catalogo: { catValor: "CAT_FUE_ING" } });
  getEstados = () =>
    this.getPorPadre({ catalogo: { catValor: "CAT_ESTADOS" } });
  getEstadosSolicitud = () =>
    this.getPorPadre({ catalogo: { catValor: "CAT_SOL_INV" } });
  getFacturacionMensual = () =>
    this.getPorPadre({ catalogo: { catValor: "CAT_FAC_MEN" } });
  gets = (props) => {
    const { navigate } = props;
    return AxiosInstance(navigate).get(getCatalogosUrl());
  };
  getsFiltro = (props) => {
    const { navigate, filtros } = props;
    return AxiosInstance(navigate).post(getCatalogosFilterUrl(), {
      ...filtros,
    });
  };
  getPorPadre = (props) => {
    const { catalogo } = props;
    const headers = {
      "Accept-Language": "en_US",
    };
    return axios.create({ headers }).post(getCatalogosPorPadreUrl(), catalogo);
  };
  // getCatalogo = (props) => {
  //     const { catalogo } = props
  //     const headers = { 'Accept-Language': 'en_US' }
  //     const axiosInstance = axios.create({
  //         headers
  //     })
  //     return axiosInstance.post(getCatalogosPorPadreUrl(), catalogo)
  // }
  get = (props) => {
    const { navigate, catalogo } = props;
    return AxiosInstance(navigate).post(getCatalogosPorIdUrl(), catalogo);
  };
  set = (props) => {
    const { navigate, catalogo } = props;
    if (
      catalogo.catId !== undefined &&
      catalogo.catId !== null &&
      catalogo.catId !== ""
    ) {
      return AxiosInstance(navigate).patch(
        getActualizarCatalogoUrl(),
        catalogo
      );
    } else {
      return AxiosInstance(navigate).put(getInsertarCatalogoUrl(), catalogo);
    }
  };
  del = (props) => {
    const { navigate, catalogo } = props;
    return AxiosInstance(navigate).post(getEliminarCatalogoUrl(), catalogo);
  };
}
