import React, { useState } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { Button, Col, Popconfirm, Row, Space, Table, Typography } from "antd";
import * as anticons from "@ant-design/icons";
import { getLabel, labels } from "../../utils/Internationalization";

const tableProps = {
  bordered: false,
  loading: false,
  title: "",
  showHeader: true,
  rowSelection: {},
  pagination: {
    position: ["none", "bottomRight"],
  },
  scroll: {
    x: "max-content",
  },
};

const columnas = [
  {
    title: "Nombres",
    fixed: "left",
    render: (_, { perId, usuId }) => (
      <Link to={`/empresas/${perId}`}>
        {usuId.nombres} {usuId.apellidos || ""}
      </Link>
    ),
  },
  {
    title: "Identificacion",
    render: (_, { usuId }) => usuId.documento,
  },
  {
    title: "Correo",
    render: (_, { usuId }) => usuId.email,
  },
  {
    title: "Tipo",
    render: (_, { perTipoPersona }) => perTipoPersona.catNombre,
  },
  {
    title: "Estado",
    render: (_, { perEstado }) => perEstado.catNombre,
  },
];

const Empresas = ({ empresas, isLoading, isDeleting, consultar, eliminar }) => {
  const [personalSeleccionados, setPersonalSeleccionados] = useState([]);
  const rowSelection = {
    onChange: (selectedRowKeys, selectedRows) => {
      setPersonalSeleccionados(selectedRows);
    },
    getCheckboxProps: (record) => ({
      disabled: record.name === "Disabled User",
      name: record.name,
    }),
  };
  return (
    <>
      <Row gutter={[16, 16]}>
        <Col span={24}>
          <Row justify="start">
            <Typography.Title level={4}>Empresas</Typography.Title>
          </Row>
        </Col>
        <Col span={24}>
          <Row justify="end">
            <Col>
              <Space>
                {personalSeleccionados.length > 0 && (
                  <Popconfirm
                    title="¿Seguro(a) que desea eliminar las empresas seleccionadas?"
                    okText="Si, eliminar"
                    cancelText="No"
                    onConfirm={eliminar}
                    okButtonProps={{
                      loading: isDeleting || isLoading,
                    }}
                  >
                    <Button
                      type="primary"
                      disabled={
                        isDeleting ||
                        isLoading ||
                        personalSeleccionados.length < 1
                      }
                      danger
                      loading={isDeleting}
                      icon={<anticons.DeleteFilled />}
                    >
                      {isDeleting
                        ? getLabel(labels.deleting)
                        : getLabel(labels.delete)}
                    </Button>
                  </Popconfirm>
                )}
                <Button
                  type="primary"
                  onClick={() => consultar()}
                  disabled={isLoading || isDeleting}
                >
                  {getLabel(labels.refresh)}
                </Button>
              </Space>
            </Col>
          </Row>
        </Col>
        <Col span={24}>
          <Table
            {...tableProps}
            rowKey="perId"
            rowSelection={{
              type: "checkbox",
              ...rowSelection,
            }}
            columns={columnas}
            dataSource={empresas}
            loading={isLoading || isDeleting}
          />
        </Col>
      </Row>
    </>
  );
};

Empresas.propTypes = {
  empresas: PropTypes.arrayOf(Object).isRequired,
  isLoading: PropTypes.bool.isRequired,
  isDeleting: PropTypes.bool.isRequired,
  consultar: PropTypes.func.isRequired,
  eliminar: PropTypes.func.isRequired,
};

export default Empresas;
