import React, { useState } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import {
  Button,
  Col,
  Popconfirm,
  Row,
  Space,
  Typography,
  Form,
  Select,
  Input,
  Checkbox,
} from "antd";
import PasswordChecklist from "react-password-checklist";
import * as anticons from "@ant-design/icons";
import { getLabel, labels } from "../../utils/Internationalization";

const { Option } = Select;
const formItemLayout = { xs: 24, sm: 12, md: 12, lg: 12, xl: 12 };

const renderCatalogo = (e) => (
  <Option key={e.catId} value={e.catId}>
    {e.catNombre}
  </Option>
);

const Empresa = ({
  empresa,
  isLoading,
  isSaving,
  isDeleting,
  tiposIdentificacion,
  tiposPersona,
  actividades,
  consultar,
  guardar,
  eliminar,
}) => {
  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  console.log(empresa)
  return (
    <>
      <Row gutter={[16, 16]}>
        <Col span={24}>
          <Row align="middle" gutter={10}>
            <Col>
              <Link to={"/empresas"}>
                <anticons.ArrowLeftOutlined />
              </Link>
            </Col>
            <Col>
              <Typography.Title
                level={4}
                style={{
                  margin: 0,
                }}
              >
                Empresa
              </Typography.Title>
            </Col>
          </Row>
        </Col>
        <Col span={24}>
          <Row justify="end">
            <Col>
              <Space>
                <Popconfirm
                  title="¿Seguro(a) que desea eliminar la empresa?"
                  okText="Si, eliminar"
                  cancelText="No"
                  onConfirm={eliminar}
                  okButtonProps={{
                    loading: isDeleting || isLoading,
                  }}
                >
                  <Button
                    type="primary"
                    disabled={isDeleting || isLoading}
                    danger
                    loading={isDeleting}
                    icon={<anticons.DeleteFilled />}
                  >
                    {isDeleting
                      ? getLabel(labels.deleting)
                      : getLabel(labels.delete)}
                  </Button>
                </Popconfirm>
                <Button
                  type="primary"
                  onClick={() => consultar()}
                  disabled={isLoading || isDeleting}
                >
                  {getLabel(labels.refresh)}
                </Button>
              </Space>
            </Col>
          </Row>
        </Col>
        <Col span={24}>
          {empresa && empresa.usuId && (
            <Form
              layout={"vertical"}
              initialValues={empresa}
              onFinish={guardar}
              disabled={isSaving}
            >
              <Row gutter={[10, 10]}>
                <Col span={24}>
                  <Form.Item
                    label="Tipo de Persona"
                    name="perIdTipPer"
                    rules={[
                      {
                        required: true,
                        message: "Seleccione el tipo de persona",
                      },
                    ]}
                  >
                    <Select>
                      {tiposPersona &&
                        tiposPersona.map((p) => renderCatalogo(p))}
                    </Select>
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={[10, 10]}>
                <Col {...formItemLayout}>
                  <Form.Item
                    label="Tipo de Identificación"
                    name="perIdTipoDoc"
                    rules={[
                      {
                        required: true,
                        message: "Selecccione el tipo de identificación",
                      },
                    ]}
                  >
                    <Select>
                      {tiposIdentificacion &&
                        tiposIdentificacion.map((p) => renderCatalogo(p))}
                    </Select>
                  </Form.Item>
                </Col>
                <Col {...formItemLayout}>
                  <Form.Item
                    label="Identificación"
                    name="perDocumento"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese su identificación",
                      },
                    ]}
                  >
                    <Input type="text" />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={[10, 10]}>
                <Col span={24}>
                  <Form.Item
                    label="Razón Social"
                    name="perRazonSocial"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese la razón social",
                      },
                    ]}
                  >
                    <Input type="text" />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={[10, 10]}>
                <Col {...formItemLayout}>
                  <Form.Item
                    label="Tipo de Actividad"
                    name="perIdActividad"
                    rules={[
                      {
                        required: true,
                        message: "Seleccione el tipo de actividad",
                      },
                    ]}
                  >
                    <Select>
                      {actividades && actividades.map((p) => renderCatalogo(p))}
                    </Select>
                  </Form.Item>
                </Col>
                <Col {...formItemLayout}>
                  <Form.Item
                    label="N° Clientes"
                    name="perClientes"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese el número de clientes",
                      },
                    ]}
                  >
                    <Input type="number" />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={[10, 10]}>
                <Col span={24}>
                  <Form.Item
                    label="Dirección"
                    name="perDireccion"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese la dirección",
                      },
                    ]}
                  >
                    <Input type="text" />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={[10, 10]}>
                <Col {...formItemLayout}>
                  <Form.Item
                    label="Teléfono"
                    name="perTelefono"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese el teléfono",
                      },
                    ]}
                  >
                    <Input type="text" />
                  </Form.Item>
                </Col>
                <Col {...formItemLayout}>
                  <Form.Item
                    label="Sitio WEB"
                    name="perSitioWeb"
                    rules={[
                      {
                        required: true,
                        type: "url",
                        message: "Ingrese el sitio web",
                      },
                    ]}
                  >
                    <Input type="text" />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={[10, 10]}>
                <Col span={24}>
                  <Form.Item
                    label="Descripción"
                    name="perDescripcion"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese la descripción",
                      },
                    ]}
                  >
                    <Input type="text" />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={[10, 10]}>
                <Col span={24}>
                  <Form.Item
                    label="Email"
                    name="perEmail"
                    rules={[
                      {
                        required: true,
                        type: "email",
                        message: "Ingrese el email",
                      },
                    ]}
                  >
                    <Input type="text" />
                  </Form.Item>
                </Col>
              </Row>
              <Row gutter={[10, 10]}>
                <Col {...formItemLayout}>
                  <Form.Item
                    label="Nombres"
                    name="perNombres"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese sus nombres",
                      },
                    ]}
                  >
                    <Input type="text" />
                  </Form.Item>
                </Col>
                <Col {...formItemLayout}>
                  <Form.Item
                    label="Apellidos"
                    name="perApellidos"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese sus apellidos",
                      },
                    ]}
                  >
                    <Input type="text" />
                  </Form.Item>
                </Col>
              </Row>

              <Row gutter={[10, 10]}>
                <Col {...formItemLayout}>
                  <Form.Item
                    label="Contraseña"
                    name="perPassword"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese la contraseña",
                      },
                    ]}
                  >
                    <Input.Password
                      onChange={(e) => setPassword(e.target.value)}
                    />
                  </Form.Item>
                </Col>
                <Col {...formItemLayout}>
                  <Form.Item
                    label="Confirme la contraseña"
                    name="perPasswordConfirm"
                    rules={[
                      {
                        required: true,
                        message: "Confirme la contraseña",
                      },
                    ]}
                  >
                    <Input.Password
                      onChange={(e) => setPasswordConfirm(e.target.value)}
                    />
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <PasswordChecklist
                    rules={[
                      "minLength",
                      "specialChar",
                      "number",
                      "capital",
                      "match",
                    ]}
                    minLength={8}
                    value={password}
                    valueAgain={passwordConfirm}
                    messages={{
                      minLength: "La contraseña tiene más de 8 caracteres.",
                      specialChar: "La contraseña tiene caracteres especiales.",
                      number: "La contraseña tiene un número.",
                      capital: "La contraseña tiene una letra mayúscula.",
                      match: "Las contraseñas coinciden.",
                    }}
                    onChange={(isValid) => {}}
                  />
                </Col>
                <Col span={24}>
                  <Form.Item
                    name="perTerCon"
                    valuePropName="checked"
                    rules={[
                      {
                        validator: (_, value) =>
                          value
                            ? Promise.resolve()
                            : Promise.reject(
                                new Error(
                                  "Acepte los términos y condiciones y el acuerdo de uso de la plataforma"
                                )
                              ),
                      },
                    ]}
                  >
                    <Checkbox>
                      Estoy de acuerdo y acepto los{" "}
                      <a
                        href="/files/terminoscondiciones.pdf"
                        target={"_blank"}
                      >
                        Términos y Condiciones
                      </a>{" "}
                      y el{" "}
                      <a href="/files/politicasuso.pdf" target={"_blank"}>
                        Acuerdo de Uso
                      </a>{" "}
                      de la Plataforma KOYN que incluye la Pólitica de
                      Privacidad aplicable al tratamiento de la información de
                      datos personales.
                    </Checkbox>
                  </Form.Item>
                </Col>
              </Row>
              <Form.Item>
                <Button
                  type="primary"
                  htmlType="submit"
                  loading={isSaving}
                  disabled={isSaving}
                  block
                >
                  {isSaving ? "Guardando..." : "Registrar"}
                </Button>
              </Form.Item>
            </Form>
          )}
        </Col>
      </Row>
    </>
  );
};

Empresa.propTypes = {
  empresa: PropTypes.object.isRequired,
  isLoading: PropTypes.bool.isRequired,
  isSaving: PropTypes.bool.isRequired,
  isDeleting: PropTypes.bool.isRequired,
  tiposIdentificacion: PropTypes.arrayOf(Object).isRequired,
  tiposPersona: PropTypes.arrayOf(Object).isRequired,
  actividades: PropTypes.arrayOf(Object).isRequired,
  consultar: PropTypes.func.isRequired,
  guardar: PropTypes.func.isRequired,
  eliminar: PropTypes.func.isRequired,
};

export default Empresa;
