import React, { useState, useEffect } from "react";
import { useHistory, Link } from "react-router-dom";
import {
  Typography,
  Button,
  Space,
  Table,
  Row,
  Col,
  Divider,
  Drawer,
  Form,
  message,
  Input,
  Popconfirm,
  Checkbox,
  InputNumber,
} from "antd";
import * as anticons from "@ant-design/icons";
import PerfilService from "../../service/PerfilService";

const perfilService = new PerfilService();

const tableProps = {
  bordered: false,
  loading: false,
  title: "",
  showHeader: true,
  rowSelection: {},
  size: "medium",
  pagination: {
    position: ["none", "none"],
  },
};

const columnas = [
  {
    title: "Nombre",
    dataIndex: "perNombre",
    render: (_, { perId, perNombre }) => (
      <Link to={`/perfil/${perId}/${perNombre}`}>{perNombre}</Link>
    ),
  },
  {
    title: "Nivel",
    dataIndex: "perNivel",
    responsive: ["xl"],
  },
  {
    title: "Estado",
    dataIndex: "perEstado",
    render: (e) => <> {e ? "Habilitado" : "Deshabilitado"} </>,
  },
];

const Perfiles = () => {
  const navigate = useHistory();
  const [load, setLoad] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);
  const [perfiles, setPerfiles] = useState([]);
  const [perfilesSeleccionados, setPerfilesSeleccionados] = useState([]);
  const [perfil, setPerfil] = useState({});
  const [perfilAbierto, setPerfilAbierto] = useState(false);
  const [formPerfil] = Form.useForm();

  const rowSelection = {
    onChange: (selectedRowKeys, selectedRows) => {
      setPerfilesSeleccionados(selectedRows);
    },
    getCheckboxProps: (record) => ({
      disabled: record.name === "Disabled User",
      name: record.name,
    }),
  };

  const onChangePerfil = (e) => {
    setPerfil({
      ...perfil,
      [e.target.name]:
        e.target.type === "checkbox" ? e.target.checked : e.target.value,
    });
  };
  const onChangePerfilNivel = (e) => {
    setPerfil({ ...perfil, perNivel: e });
  };

  const fetchData = async () => {
    setLoad(false);
    setIsLoading(true);
    try {
      const { data } = await perfilService.gets({ navigate });
      if (data) {
        setPerfiles(data.listObject.filter((p) => p !== null));
      } else {
        message.error("Hubo un error al obtener los perfiles");
      }
    } catch (error) {
      message.error("No pudimos hacer la solicitud para obtener los perfiles");
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    if (load) {
      fetchData();
    }
  });

  const getNuevoPerfil = () => ({
    perEstado: true,
    perNivel: "",
    perNombre: "",
  });

  const abrirPerfil = (c) => {
    setPerfil({
      ...c,
    });
    setPerfilAbierto(true);
  };

  const cerrarPerfil = () => {
    setPerfilAbierto(false);
  };

  const guardarPerfil = async () => {
    setIsSaving(true);
    try {
      const { data } = await perfilService.put({ navigate, perfil });
      setIsSaving(false);
      if (data) {
        setPerfil(data.listObject[0]);
        message.success("Perfil guardado correctamente");
        setIsLoading(true);
      }
    } catch (error) {
      setIsSaving(false);
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para guardar el perfil"
      );
    }
  };

  const eliminarPerfiles = (e) => {
    const requests = [];
    perfilesSeleccionados.forEach((c) => {
      requests.push(perfilService.del({ navigate, perfil: { ...c } }));
    });
    setIsDeleting(true);
    Promise.all(requests)
      .then((results) => {
        setPerfilesSeleccionados([]);
        setIsDeleting(false);
        setIsLoading(true);
      })
      .catch((error) => {
        setIsSaving(false);
        const { response } = error;
        const msgError =
          response.status === 409
            ? response.data[0].messageText
            : response.data;
        message.error(`No fue posible eliminar los perfiles: ${msgError}`);
      });
  };

  const eliminarPerfil = async () => {
    setIsDeleting(true);
    try {
      const { data } = await perfilService.del({ navigate, perfil });
      setIsDeleting(false);
      if (data === true) {
        setIsLoading(true);
        message.success("Perfil eliminado correctamente");
        cerrarPerfil();
      } else {
        message.error("No fue posible eliminar el perfil");
      }
    } catch (error) {
      setIsDeleting(false);
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para eliminar el perfil"
      );
    }
  };

  return (
    <>
      <Row gutter={[10, 10]}>
        <Col xs={24} sm={12} md={12} lg={12} xl={12}>
          <Row justify="start">
            <Typography.Title
              level={4}
              style={{
                margin: 0,
              }}
            >
              Gestión de Perfiles
            </Typography.Title>
          </Row>
        </Col>
        <Col xs={24} sm={12} md={12} lg={12} xl={12}>
          <Row justify="end">
            <Space>
              <Button
                type="default"
                onClick={(e) => abrirPerfil(getNuevoPerfil())}
                icon={<anticons.PlusOutlined />}
              >
                Nuevo
              </Button>
              {perfilesSeleccionados && perfilesSeleccionados.length > 0 && (
                <Popconfirm
                  title="¿Seguro(a) que desea eliminar los perfiles seleccionados?"
                  okText="Si, eliminar"
                  cancelText="No"
                  onConfirm={() => eliminarPerfiles()}
                  okButtonProps={{
                    loading: isDeleting,
                  }}
                >
                  <Button
                    type="danger"
                    disabled={isLoading || isDeleting}
                    loading={isDeleting}
                    icon={<anticons.DeleteFilled />}
                  >
                    {isDeleting ? "Eliminando" : "Eliminar"}
                  </Button>
                </Popconfirm>
              )}
              <Button
                type="primary"
                onClick={fetchData}
                loading={isLoading}
                icon={<anticons.SearchOutlined />}
              >
                Consultar
              </Button>
            </Space>
          </Row>
        </Col>
      </Row>
      <Divider></Divider>
      <Row>
        <Col span={24}>
          <Table
            {...tableProps}
            rowKey="perId"
            rowSelection={{
              type: "checkbox",
              ...rowSelection,
            }}
            columns={columnas}
            dataSource={perfiles}
            loading={isLoading}
            style={{
              width: "100%",
            }}
          />
        </Col>
      </Row>
      <Drawer
        title="Perfil"
        placement="right"
        closable={true}
        onClose={cerrarPerfil}
        open={perfilAbierto}
      >
        <Form
          layout={"vertical"}
          form={formPerfil}
          initialValues={{
            layout: "vertical",
          }}
        >
          <Row>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              <Form.Item label="Nombre">
                <Input
                  id="perNombre"
                  name="perNombre"
                  label="Nombre"
                  type="text"
                  value={perfil.perNombre}
                  onChange={onChangePerfil}
                />
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              <Form.Item label="Nivel">
                <InputNumber
                  id="perNivel"
                  name="perNivel"
                  label="Nivel"
                  min={1}
                  max={50}
                  value={perfil.perNivel}
                  onChange={onChangePerfilNivel}
                />
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              <Form.Item label="Estado">
                <Checkbox
                  id="perEstado"
                  name="perEstado"
                  onChange={onChangePerfil}
                  checked={perfil.perEstado}
                >
                  {perfil.perEstado ? "Habilitado" : "Deshabilitado"}
                </Checkbox>
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Item>
                <Space>
                  <Button
                    type="primary"
                    onClick={(e) => guardarPerfil(e)}
                    disabled={isSaving || isDeleting}
                    loading={isSaving}
                    icon={<anticons.SaveFilled />}
                  >
                    {isSaving ? "Guardando" : "Guardar"}
                  </Button>
                  {perfil && perfil.perId && (
                    <Popconfirm
                      title="¿Seguro(a) que desea eliminar el perfil?"
                      okText="Si, eliminar"
                      cancelText="No"
                      onConfirm={() => eliminarPerfil()}
                      okButtonProps={{
                        loading: isDeleting,
                      }}
                    >
                      <Button
                        type="danger"
                        disabled={isLoading || isDeleting || isSaving}
                        loading={isDeleting}
                        icon={<anticons.DeleteFilled />}
                      >
                        {isDeleting ? "Eliminando" : "Eliminar"}
                      </Button>
                    </Popconfirm>
                  )}
                </Space>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Drawer>
    </>
  );
};

export default Perfiles;
