export const getCulture = () => {
  const domainCulture =
    window.location.hostname.indexOf("koyn") > 0 ? "en_US" : "es_EC";
  return (
    (localStorage.getItem("ckCulture") &&
      JSON.parse(localStorage.getItem("ckCulture")).culture) ||
    domainCulture
  );
};
export const setCulture = (c) =>
  localStorage.setItem("ckCulture", JSON.stringify(c));
