import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import {
  Typography,
  Button,
  Input,
  Row,
  Col,
  Form,
  InputNumber,
  DatePicker,
  Collapse,
  Select,
  Divider,
  message,
} from "antd";
import * as anticons from "@ant-design/icons";
import moment from "moment/moment";
import { getLocale } from "../../utils/Utils";
import AlertWait from "../../pages/AlertWait";
import InversionistaService from "./../../service/InversionistaService";
import CatalogoService from "./../../service/CatalogoService";

const { Panel } = Collapse;
const { Option } = Select;

const inversionistaService = new InversionistaService();
const catalogoService = new CatalogoService();

const Inversionista = (props) => {
  const { navigate } = props;
  const [bancos, setBancos] = useState([]);
  const [tiposCuenta, setTiposCuenta] = useState([]);
  const [fuentesIngreso, setFuentesIngreso] = useState([]);
  const [tiposIdentificacion, setTiposIdentificacion] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isSaving, setIsSaving] = useState(false);
  const [error, setError] = useState(null);
  const [inversionista, setInversionista] = useState(null);
  const [formRegistro] = Form.useForm();

  const get = async () => {
    Promise.all([
      catalogoService.getTiposIdentificacion(navigate),
      catalogoService.getBancos(navigate),
      catalogoService.getTiposCuenta(navigate),
      catalogoService.getFuentesIngreso(navigate),
      inversionistaService.get(props),
    ])
      .then((r) => {
        setIsLoading(false);
        setError("");
        const tiposIdentificacion = r[0].data.listObject;
        const bancos = r[1].data.listObject;
        const tiposCuenta = r[2].data.listObject;
        const fuentesIngreso = r[3].data.listObject;
        const inversionista = r[4].data.listObject.map((i) => ({
          ...i,
          ...{
            invFecNac: moment(i.invFecNac, "DD/MM/YYYY", true),
            invTitCueBan: `${i.usuId.nombres} ${i.usuId.apellidos}`,
          },
        }))[0];
        setTiposIdentificacion(tiposIdentificacion);
        setBancos(bancos);
        setTiposCuenta(tiposCuenta);
        setFuentesIngreso(fuentesIngreso);
        setInversionista(inversionista);
      })
      .catch((e) => {
        console.log(e);
        setIsLoading(false);
        setError("No pudimos hacer la solicitud para obtener el inversionista");
      });
  };

  const actualizar = async (values) => {
    const datos = {
      invId: inversionista.invId,
      invPais: values.invPais,
      invCiudad: values.invCiudad,
      invFecNac: values.invFecNac,
      invIdFuenteIngresos: values.invFuenteIngresos.catId,
      invCargo: values.invCargo,
      invIngresos: values.invIngresos,
    };
    setIsSaving(true);
    try {
      const { data } = await inversionistaService.set({
        navigate,
        inversionista: { ...datos },
      });
      console.log(data);
    } catch (error) {
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "Lo sentimos, ocurrió un error."
      );
    } finally {
      setIsSaving(false);
    }
  };

  const renderCatalogo = (e) => (
    <Option key={e.catId} value={e.catId}>
      {e.catNombre}
    </Option>
  );
  const renderCatalogoValor = (e) => (
    <Option key={e.catValor} value={e.catValor}>
      {e.catNombre}
    </Option>
  );

  useEffect(() => {
    if (isLoading) {
      get();
    }
  });

  return (
    <>
      <AlertWait error={error} isLoading={isLoading} />
      <Row>
        <Col span={24}>
          <Row justify="start">
            <Typography.Title
              level={4}
              style={{
                margin: 0,
              }}
            >
              Inversionista
            </Typography.Title>
          </Row>
        </Col>
      </Row>
      <Divider></Divider>
      {inversionista && (
        <Form
          layout={"vertical"}
          form={formRegistro}
          initialValues={inversionista}
          onFinish={actualizar}
        >
          <Collapse defaultActiveKey={["1"]} bordered={false} accordion>
            <Panel
              key="1"
              header={
                <Typography.Text strong>Información Personal</Typography.Text>
              }
              extra={
                <Typography.Text type="success">
                  <anticons.CheckCircleOutlined />
                </Typography.Text>
              }
            >
              <Row justify="center" gutter={10}>
                <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                  <Form.Item
                    label="Nombres"
                    name={["usuId", "nombres"]}
                    rules={[
                      {
                        required: true,
                        message: "Ingrese sus nombres",
                      },
                    ]}
                  >
                    <Input
                      id="invNombres"
                      name="invNombres"
                      label="Nombres"
                      type="text"
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                  <Form.Item
                    label="Apellidos"
                    name={["usuId", "apellidos"]}
                    rules={[
                      {
                        required: true,
                        message: "Ingrese sus apellidos",
                      },
                    ]}
                  >
                    <Input
                      id="invApellidos"
                      name="invApellidos"
                      label="Apellidos"
                      type="text"
                    />
                  </Form.Item>
                </Col>
              </Row>
              <Row justify="center" gutter={10}>
                <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                  <Form.Item
                    label="Tipo de Identificación"
                    name={["usuId", "idTipoDocumento"]}
                    rules={[
                      {
                        required: true,
                        message: "Seleccione el tipo de identificación",
                      },
                    ]}
                  >
                    <Select name="invTipoDoc">
                      {tiposIdentificacion &&
                        tiposIdentificacion.map((p) => renderCatalogoValor(p))}
                    </Select>
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                  <Form.Item
                    label="Identificación"
                    name={["usuId", "documento"]}
                    rules={[
                      {
                        required: true,
                        message: "Ingrese su identificación",
                      },
                    ]}
                  >
                    <Input
                      id="invDocumento"
                      name="invDocumento"
                      label="Identificación"
                      type="text"
                    />
                  </Form.Item>
                </Col>
              </Row>
              <Row justify="center" gutter={10}>
                <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                  <Form.Item
                    label="Dirección"
                    name="invDireccion"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese su dirección",
                      },
                    ]}
                  >
                    <Input
                      id="invDireccion"
                      name="invDireccion"
                      label="Dirección"
                      type="text"
                    />
                  </Form.Item>
                </Col>
              </Row>
              <Row justify="center" gutter={10}>
                <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                  <Form.Item
                    label="Ciudad"
                    name="invCiudad"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese la ciudad",
                      },
                    ]}
                  >
                    <Input
                      id="invCiudad"
                      name="invCiudad"
                      label="Ciudad"
                      type="text"
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                  <Form.Item
                    label="País"
                    name="invPais"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese el país",
                      },
                    ]}
                  >
                    <Input
                      id="invPais"
                      name="invPais"
                      label="País"
                      type="text"
                    />
                  </Form.Item>
                </Col>
              </Row>
              <Row justify="center" gutter={10}>
                <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                  <Form.Item
                    label="Fecha de Nacimiento"
                    name="invFecNac"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese la fecha de nacimiento",
                      },
                    ]}
                  >
                    <DatePicker
                      id="invFecNac"
                      name="invFecNac"
                      format={["DD/MM/YYYY"]}
                      locale={getLocale()}
                      style={{ width: "100%" }}
                    ></DatePicker>
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                  <Form.Item
                    label="Teléfono"
                    name="invTelefono"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese el teléfono",
                      },
                    ]}
                  >
                    <Input
                      id="invTelefono"
                      name="invTelefono"
                      label="Teléfono"
                      type="text"
                    />
                  </Form.Item>
                </Col>
              </Row>
            </Panel>
            <Panel
              key="2"
              header={
                <Typography.Text strong>Fuente de Ingresos</Typography.Text>
              }
              extra={
                <Typography.Text type="success">
                  <anticons.CheckCircleOutlined />
                </Typography.Text>
              }
            >
              <Row gutter={[10, 10]}>
                <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                  <Form.Item
                    label="Principal Fuente de ingresos"
                    name={["invFuenteIngresos", "catId"]}
                    rules={[
                      {
                        required: true,
                        message: "Seleccione la fuente de ingresos",
                      },
                    ]}
                  >
                    <Select>
                      {fuentesIngreso &&
                        fuentesIngreso.map((p) => renderCatalogo(p))}
                    </Select>
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                  <Form.Item
                    label="Cargo"
                    name="invCargo"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese el cargo",
                      },
                    ]}
                  >
                    <Input
                      id="invCar"
                      name="invCar"
                      label="Cargo"
                      type="text"
                    />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                  <Form.Item
                    label="Ingresos mensuales aproximados"
                    name="invIngresos"
                    rules={[
                      {
                        required: true,
                        message: "Complete los ingresos",
                      },
                    ]}
                  >
                    <InputNumber
                      formatter={(value) =>
                        `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
                      style={{ width: "100%" }}
                    />
                  </Form.Item>
                </Col>
              </Row>
            </Panel>
            <Panel
              key="3"
              header={<Typography.Text strong>Cuenta Bancaria</Typography.Text>}
              extra={
                <Typography.Text type="success">
                  <anticons.CheckCircleOutlined />
                </Typography.Text>
              }
            >
              <Typography.Paragraph>
                <Typography.Text type="secondary">
                  Donde recibiras tus ganancias
                </Typography.Text>
              </Typography.Paragraph>
              <Row gutter={[10, 10]}>
                <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                  <Form.Item
                    label="Titular"
                    name="invTitCueBan"
                    rules={[
                      {
                        required: true,
                        message: "Titular",
                      },
                    ]}
                  >
                    <Input type="text" />
                  </Form.Item>
                </Col>
                <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                  <Form.Item
                    label="Banco"
                    name="invBan"
                    rules={[
                      {
                        required: true,
                        message: "Seleccione el banco",
                      },
                    ]}
                  >
                    <Select name="invBan">
                      {bancos && bancos.map((p) => renderCatalogo(p))}
                    </Select>
                  </Form.Item>
                </Col>
                <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                  <Form.Item
                    label="Tipo Cuenta"
                    name="invTipCue"
                    rules={[
                      {
                        required: true,
                        message: "Seleccione el tipo de cuenta",
                      },
                    ]}
                  >
                    <Select name="invTipCue">
                      {tiposCuenta && tiposCuenta.map((p) => renderCatalogo(p))}
                    </Select>
                  </Form.Item>
                </Col>
                <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                  <Form.Item
                    label="Número de Cuenta"
                    name="invNumCueBan"
                    rules={[
                      {
                        required: true,
                        message: "Ingrese el número de cuenta",
                      },
                    ]}
                  >
                    <Input
                      id="invNumCueBan"
                      name="invNumCueBan"
                      label="Número de Cuenta"
                      type="text"
                    />
                  </Form.Item>
                </Col>
              </Row>
            </Panel>
          </Collapse>
          <Divider></Divider>
          <Button
            type="primary"
            htmlType="submit"
            loading={isLoading || isSaving}
            disabled={isLoading || isSaving}
            block
          >
            Guardar
          </Button>
        </Form>
      )}
    </>
  );
};

Inversionista.propTypes = {
  inversionista: PropTypes.object.isRequired,
  navigate: PropTypes.object.isRequired,
};

export default Inversionista;
