import React from "react";
import { Row, Col, Typography, Avatar, Button, Space } from "antd";
import { getFechaIngreso, getNombreUsuario } from "../utils/Utils";

const Welcome = () => {
  return (
    <>
      <Row gutter={[16, 16]}>
        <Col>
          <Avatar src="https://api.dicebear.com/7.x/miniavs/svg?seed=1" />
        </Col>
        <Col span={24}>
          <Row>
            <Col span={24}>
              <Typography.Title level={5}>Bienvenido a Koyn</Typography.Title>
            </Col>
            <Col span={24}>
              <Typography.Title level={2}>
                {getNombreUsuario()}
              </Typography.Title>
            </Col>
            <Col span={24}>
              <Typography.Text>
                {`Your last login was on ${getFechaIngreso()}`}
              </Typography.Text>
            </Col>
          </Row>
        </Col>
        <Col span={24}>
          <Row gutter={[16, 16]}>
            <Col span={24}>
              <Space direction="vertical">
                <Button type="link" href="#/myaccount">
                  Revisa y actualiza tu información y datos de contacto.
                </Button>
                <Button type="link" href="#/myenterprises">
                  Revisa las empresas que tienes asociadas a KOYN.
                </Button>
              </Space>
            </Col>
          </Row>
        </Col>
      </Row>
    </>
  );
};

export default Welcome;
