import AxiosInstance from "../utils/AxiosInstance";
import {
  getInversionistaPorIdUrl,
  getInversionistasUrl,
  getUpdateInvestorUrl,
} from "../utils/urls";

export default class InversionistaService {
  get = (props) => {
    const { navigate, inversionista } = props;
    return AxiosInstance(navigate).post(
      getInversionistaPorIdUrl(),
      inversionista
    );
  };
  gets = (props) => {
    const { navigate } = props;
    return AxiosInstance(navigate).get(getInversionistasUrl());
  };
  set = (props) => {
    const { navigate, inversionista } = props;
    return AxiosInstance(navigate).post(getUpdateInvestorUrl(), inversionista);
  };
}
