import React from "react";
import { Row, Col, Image } from "antd";

const Logo = (props) => {
  return (
    <Row>
      <Col
        span={24}
        style={{
          padding: '20px 0px 0px 0px',
          textAlign: 'center'
        }}
      >
       <Image alt="" src="./assets/logo.svg" preview={false}></Image>
      </Col>
    </Row>
  );
};

export default Logo;
