import React from "react";
import { useHistory } from "react-router-dom";
import { getIsInvertor, getIsPerson, getResponsable } from "../../utils/Utils";
import Inversionista from "../inversionista/Inversionista";
import Persona from "../persona/Persona";

const MiCuenta = () => {
  const navigate = useHistory();
  const id = getResponsable();
  return (
    <>
      {getIsInvertor() ? (
        <Inversionista inversionista={{ invId: id }} navigate={navigate} />
      ) : getIsPerson() ? (
        <Persona persona={{ perId: id }} navigate={navigate} />
      ) : (
        ""
      )}
    </>
  );
};

export default MiCuenta;
