import React, { useEffect, useState } from "react";
import { useHistory, useParams, Link } from "react-router-dom";
import {
  Col,
  Row,
  Button,
  Form,
  Input,
  Checkbox,
  Spin,
  Typography,
  Divider,
  message,
  Popconfirm,
  Space,
  InputNumber,
  Table,
  Switch,
} from "antd";
import * as anticons from "@ant-design/icons";
import PerfilService from "../../service/PerfilService";
import OpcionService from "../../service/OpcionService";
import PerfilOpcionService from "../../service/PerfilOpcionService";

const perfilService = new PerfilService();
const opcionService = new OpcionService();
const perfilOpcionService = new PerfilOpcionService();

const Perfil = () => {
  const navigate = useHistory();
  const { id } = useParams();
  const [load, setLoad] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [isLoadingOpciones, setIsLoadingOpciones] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);
  const [perfil, setPerfil] = useState({});
  const [form] = Form.useForm();

  const [opciones, setOpciones] = useState([]);

  const columns = [
    {
      title: "Nombre Opción",
      dataIndex: "opcNombre",
      key: "idOpcion",
    },
    {
      title: "Lectura",
      dataIndex: "opcLectura",
      render: (text, record, index) => (
        <Switch
          onChange={() => onChangePerfil(text, record, "opcLectura")}
          defaultChecked={record.opcLectura}
        />
      ),
    },
    {
      title: "Escritura",
      dataIndex: "opcEscritura",
      render: (text, record, index) => (
        <Switch
          onChange={() => onChangePerfil(text, record, "opcEscritura")}
          defaultChecked={record.opcEscritura}
        />
      ),
    },
  ];

  const onChangePerfil = async (text, record, operacion) => {
    if (operacion === "opcLectura") {
      record.opcLectura = !record.opcLectura;
    } else if (operacion === "opcEscritura") {
      record.opcEscritura = !record.opcEscritura;
    }
    setIsSaving(true);
    try {
      const { data } = await perfilOpcionService.set({
        navigate,
        perfilOpcion: record,
      });
      setIsSaving(false);
      const perfilOpcionActualizado = data.listObject[0];
      message.success(
        `Opción ${perfilOpcionActualizado.pfoOpcId.opcNombre} actualizada correctamente`
      );
    } catch (error) {
      setIsSaving(false);
      message.error(
        "No pudimos hacer la solicitud para guardar la opción del perfil"
      );
    }
  };

  const onChange = (e) => {
    setPerfil({
      ...perfil,
      [e.target.name]:
        e.target.type === "checkbox" ? e.target.checked : e.target.value,
    });
  };

  const onChangePerfilNivel = (e) => {
    setPerfil({ ...perfil, perNivel: e });
  };

  const getPerfil = async () => {
    setLoad(false);
    setIsLoading(true);
    try {
      const { data } = await perfilService.get({
        navigate,
        perfil: { perId: id },
      });
      if (data) {
        setPerfil(data.listObject[0]);
        getOpcionesPerfil();
      } else {
        message.error("Hubo un error al obtener el perfil");
      }
    } catch (error) {
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para obtener el perfil"
      );
    } finally {
      setIsLoading(false);
    }
  };

  const getOpcionesPerfil = async () => {
    setIsLoadingOpciones(true);
    try {
      const { data } = await perfilOpcionService.gets({ navigate });
      if (data) {
        var _opcionesPerfil = data.listObject[0].filter(
          (o) => o.idPerfil === parseInt(id)
        );
        try {
          const { data } = await opcionService.gets({ navigate });
          if (data) {
            const opciones = data.listObject
              .filter((o) => o.opcNivel === 1)
              .map((o) => ({
                key: `${o.opcId}`,
                opcNombre: o.opcNombre,
                ..._opcionesPerfil.filter((op) => op.idOpcion === o.opcId)[0],
                children: data.listObject
                  .filter((oh) => oh.opcPadre && oh.opcPadre.opcId === o.opcId)
                  .map((o) => ({
                    key: `${o.opcId}`,
                    opcNombre: o.opcNombre,
                    ..._opcionesPerfil.filter(
                      (op) => op.idOpcion === o.opcId
                    )[0],
                  })),
              }));
            setOpciones(opciones);
          } else {
            message.error("Hubo un error al obtener los opciones");
          }
        } catch (error) {
          message.error(
            "No pudimos hacer la solicitud para obtener los opciones"
          );
        }
      } else {
        message.error("Hubo un error al obtener las opciones del perfil");
      }
    } catch (error) {
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para obtener las opciones del perfil"
      );
    } finally {
      setIsLoadingOpciones(false);
    }
  };

  const guardar = async (e) => {
    setIsSaving(true);
    try {
      const { data } = await perfilService.set({ navigate, perfil });
      setIsSaving(false);
      const perfilActualizado = data.listObject[0];
      setPerfil(perfilActualizado);
      message.success("Perfil guardado correctamente");
    } catch (error) {
      setIsSaving(false);
      message.error("No pudimos hacer la solicitud para guardar el perfil");
    }
  };

  const eliminar = async (e) => {
    setIsDeleting(true);
    try {
      const { data } = await perfilService.del({ navigate, perfil });
      setIsDeleting(false);
      if (data === true) {
        message.success("Perfil eliminado");
        navigate.push("/perfiles");
      } else {
        message.error("No fue posible eliminar el perfil");
      }
    } catch (error) {
      setIsDeleting(false);
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para eliminar el perfil"
      );
    }
  };

  useEffect(() => {
    if (load) {
      getPerfil();
    }
  });

  return (
    <>
      <Row align="middle" gutter={10}>
        <Col>
          <Link to={"/perfiles"}>
            <anticons.ArrowLeftOutlined />
          </Link>
        </Col>
        <Col>
          <Typography.Title
            level={4}
            style={{
              margin: 0,
            }}
          >
            Perfil {perfil && perfil.perNombre}
          </Typography.Title>
        </Col>
      </Row>
      <Divider></Divider>
      {isLoading ? (
        <div className="p-10 text-center">
          <Spin size="large" />
          <br />
          <Typography.Text>Espere un momento...</Typography.Text>
        </div>
      ) : (
        <Form
          layout={"vertical"}
          form={form}
          initialValues={{
            layout: "vertical",
          }}
        >
          <Row gutter={10}>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              <Form.Item label="Nombre">
                <Input
                  id="perNombre"
                  name="perNombre"
                  label="Nombre"
                  type="text"
                  value={perfil.perNombre}
                  onChange={onChange}
                />
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              <Form.Item label="Nivel">
                <InputNumber
                  id="perNivel"
                  name="perNivel"
                  label="Nivel"
                  min={1}
                  max={50}
                  value={perfil.perNivel}
                  onChange={onChangePerfilNivel}
                />
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              <Form.Item label="Estado">
                <Checkbox
                  id="perEstado"
                  name="perEstado"
                  onChange={onChange}
                  checked={perfil.perEstado}
                >
                  {perfil.perEstado ? "Habilitado" : "Deshabilitado"}
                </Checkbox>
              </Form.Item>
            </Col>
          </Row>
          <Divider>Opciones</Divider>
          <Row>
            <Col span={24}>
              <Table
                columns={columns}
                dataSource={opciones}
                loading={isLoadingOpciones || isSaving}
              />
            </Col>
          </Row>
          <Row justify="end">
            <Col>
              <Form.Item>
                <Space>
                  <Button
                    type="primary"
                    onClick={(e) => guardar(e)}
                    disabled={isSaving || isDeleting}
                    loading={isSaving}
                    icon={<anticons.SaveFilled />}
                  >
                    {isSaving ? "Guardando" : "Guardar"}
                  </Button>
                  <Popconfirm
                    title="¿Seguro(a) que desea eliminar el perfil?"
                    okText="Si, eliminar"
                    cancelText="No"
                    onConfirm={(e) => eliminar(e)}
                    okButtonProps={{
                      loading: isDeleting,
                    }}
                  >
                    <Button
                      type="danger"
                      disabled={isDeleting || isSaving}
                      loading={isDeleting}
                      icon={<anticons.DeleteFilled />}
                    >
                      {isDeleting ? "Eliminando" : "Eliminar"}
                    </Button>
                  </Popconfirm>
                </Space>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      )}
    </>
  );
};

export default Perfil;
