import React from "react";
import Empresas from "../../components/empresa/Empresas";
import usePersonas from "../../hooks/usePersonas";

const EmpresasPage = () => {
  const { empresas, isLoading, isDeleting, consultar, eliminar } =
    usePersonas();
  const acciones = { consultar, eliminar };
  const propiedades = { empresas, isLoading, isDeleting };
  return <Empresas {...propiedades} {...acciones} />;
};

export default EmpresasPage;
