import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { message } from "antd";

import PersonaService from "../service/PersonaService";

const personaService = new PersonaService();

const usePersonas = () => {
  const navigate = useHistory();
  const [empresas, setEmpresas] = useState([]);
  const [load, setLoad] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);

  const consultar = async () => {
    setLoad(false);
    setIsLoading(true);
    try {
      const { data } = await personaService.gets({ navigate });
      setEmpresas(data.listObject);
      if (data.listObject.length <= 0)
        message.info("No se encontraron resultados");
    } catch ({ response }) {
      const { data } = response;
      message.error(
        response.status === 409
          ? data[0].messageText
          : "No fue posible consultar las empresas"
      );
    } finally {
      setIsLoading(false);
    }
  };

  const eliminar = async () => {
    setIsDeleting(true);
    setTimeout(() => {
      setIsDeleting(false);
      message.success("Empresa eliminada");
    }, 2000);
  };

  useEffect(() => {
    if (load) consultar();
  });

  return { empresas, isLoading, isDeleting, consultar, eliminar };
};

export default usePersonas;
