import React from "react";
import { useHistory } from "react-router-dom";
import { Card, Result, Button } from "antd";

const ResultRegistro = () => {
  const navigate = useHistory();
  const login = () => {
    navigate.push("/login");
  };
  return (
    <Card
      style={{
        margin: "40px 0px 40px 0px",
      }}
    >
      <Result
        status="success"
        title="Gracias por registrarte en breve te contactaremos."
        extra={[
          <Button type="primary" key="console" onClick={() => login()}>
            Iniciar Sesión
          </Button>,
        ]}
      ></Result>
    </Card>
  );
};

export default ResultRegistro;
