import React from "react";
import { useParams } from "react-router-dom";
import Empresa from "../../components/empresa/Empresa";
import usePersona from "../../hooks/usePersona";

const EmpresaPage = () => {
  const { id } = useParams();
  const empresaInicial = { perId: id };
  const {
    empresa,
    isLoading,
    isSaving,
    isDeleting,
    tiposIdentificacion,
    tiposPersona,
    actividades,
    consultar,
    guardar,
    eliminar,
  } = usePersona(empresaInicial);
  const acciones = { consultar, guardar, eliminar };
  const propiedades = {
    empresa,
    isLoading,
    isSaving,
    isDeleting,
    tiposIdentificacion,
    tiposPersona,
    actividades,
  };
  return <Empresa {...propiedades} {...acciones} />;
};

export default EmpresaPage;
