import { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { message } from "antd";

import InversionistaService from "../service/InversionistaService";

const inversionistaService = new InversionistaService();

const useInversionistas = () => {
  const navigate = useHistory();
  const [inversionistas, setInversionistas] = useState([]);
  const [load, setLoad] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);

  const consultar = async () => {
    setLoad(false);
    setIsLoading(true);
    try {
      const { data } = await inversionistaService.gets({ navigate });
      setInversionistas(data.listObject);
      if (data.listObject.length <= 0)
        message.info("No se encontraron resultados");
    } catch ({ response }) {
      const { data } = response;
      message.error(
        response.status === 409
          ? data[0].messageText
          : "No fue posible consultar los inversionistas"
      );
    } finally {
      setIsLoading(false);
    }
  };

  const eliminar = async () => {
    setIsDeleting(true);
    setTimeout(() => {
      setIsDeleting(false);
      message.success("Inversionista eliminado");
    }, 2000);
  };

  useEffect(() => {
    if (load) consultar();
  });

  return { inversionistas, isLoading, isDeleting, consultar, eliminar };
};

export default useInversionistas;
