import React from "react";
import Inversionistas from "../../components/inversionista/Inversionistas";
import useInversionistas from "../../hooks/useInversionistas";

const InversionistasPage = () => {
  const { inversionistas, isLoading, isDeleting, consultar, eliminar } =
    useInversionistas();
  const acciones = { consultar, eliminar };
  const propiedades = { inversionistas, isLoading, isDeleting };
  return <Inversionistas {...propiedades} {...acciones} />;
};

export default InversionistasPage;
