import AxiosInstance from "./../utils/AxiosInstance";
import {
  getPerfilesUrl,
  getActualizarPerfilUrl,
  getInsertarPerfilUrl,
  getEliminarPerfilUrl,
  getPerfilPorIdUrl,
} from "./../utils/urls";
export default class PerfilService {
  gets = (props) => {
    const { navigate } = props;
    return AxiosInstance(navigate).get(getPerfilesUrl());
  };
  get = (props) => {
    const { navigate, perfil } = props;
    return AxiosInstance(navigate).post(getPerfilPorIdUrl(), perfil);
  };
  put = (props) => {
    const { navigate, perfil } = props;
    return AxiosInstance(navigate).put(getInsertarPerfilUrl(), perfil);
  };
  set = (props) => {
    const { navigate, perfil } = props;
    return AxiosInstance(navigate).patch(getActualizarPerfilUrl(), perfil);
  };
  del = (props) => {
    const { navigate, perfil } = props;
    return AxiosInstance(navigate).post(getEliminarPerfilUrl(), perfil);
  };
}
