import React, { useEffect, useState } from "react";
import { useHistory, useParams, Link } from "react-router-dom";
import {
  Col,
  Row,
  Button,
  Form,
  Input,
  Table,
  Checkbox,
  Spin,
  Typography,
  Divider,
  message,
  Popconfirm,
  Drawer,
  Space,
} from "antd";
import * as anticons from "@ant-design/icons";
import CatalogoService from "../../service/CatalogoService";

const catalogoService = new CatalogoService();

const Catalogo = () => {
  const navigate = useHistory();
  const { id, nombre } = useParams();
  const [load, setLoad] = useState(true);
  const [isLoading, setIsLoading] = useState(true);
  const [isSaving, setIsSaving] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);
  const [catalogo, setCatalogo] = useState({});
  const [subCatalogo, setSubCatalogo] = useState({});
  const [catalogos, setCatalogos] = useState([]);
  const [subCatalogoAbierto, setSubCatalogoAbierto] = useState(false);
  const [form] = Form.useForm();
  const [formSubCatalogo] = Form.useForm();

  const columnas = [
    {
      title: "Acciones",
      dataIndex: "catId",
      render: (_, record) =>
        catalogos.length >= 1 ? (
          <>
            <Button
              type="link"
              icon={<anticons.EditFilled />}
              onClick={() => abrirSubCatalogo(record)}
            ></Button>
            <Popconfirm
              title="¿Seguro(a) que desea eliminar el catálogo?"
              okText="Si, eliminar"
              cancelText="No"
              onConfirm={() => quitarSubCatalogo(record)}
              okButtonProps={{
                loading: isDeleting,
              }}
            >
              <Button
                type="link"
                icon={<anticons.DeleteFilled />}
                danger
              ></Button>
            </Popconfirm>
          </>
        ) : (
          <></>
        ),
    },
    {
      title: "Nombre",
      dataIndex: "catNombre",
    },
    {
      title: "Estado",
      dataIndex: "catEstado",
      render: (e) => <> {e ? "Habilitado" : "Deshabilitado"} </>,
    },
  ];

  const getNuevoSubCatalogo = () => ({
    catDescripcion: "",
    catEstado: true,
    catNombre: "",
    catValor: "",
  });

  const abrirSubCatalogo = (c) => {
    setSubCatalogo({
      ...c,
      catIdPadre: id,
    });
    setSubCatalogoAbierto(true);
  };

  const cerrarSubCatalogo = () => {
    setSubCatalogoAbierto(false);
  };

  const getCatalogo = async () => {
    setLoad(false);
    setIsLoading(true);
    try {
      const { data } = await catalogoService.get({
        navigate,
        catalogo: { catId: id },
      });
      if (data) {
        setCatalogo(data.listObject[0]);

        catalogoService
          .getPorPadre({
            navigate,
            catalogo: { catValor: nombre },
          })
          .then((response) => {
            const { data } = response;
            if (data) {
              setCatalogos(data.listObject);
              setIsLoading(false);
            } else {
              setIsLoading(false);
              message.error("Hubo un error al obtener los catalogos");
            }
          })
          .catch((error) => {
            setCatalogos([]);
            const { response } = error;
            if (
              !response.data[0].messageCode === 1002 &&
              response.data[0].messageText === "response.data[0].messageText"
            ) {
              message.error(
                response.status === 409
                  ? response.data[0].messageText
                  : "No pudimos hacer la solicitud para obtener los catálogos"
              );
            }
          });
      } else {
        message.error("Hubo un error al obtener el catálogo");
      }
    } catch (error) {
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para obtener el catálogo"
      );
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    if (load) {
      getCatalogo();
    }
  });

  const onChange = (e) => {
    setCatalogo({
      ...catalogo,
      [e.target.name]:
        e.target.type === "checkbox" ? e.target.checked : e.target.value,
    });
  };

  const onChangeSubCatalogo = (e) => {
    setSubCatalogo({
      ...subCatalogo,
      [e.target.name]:
        e.target.type === "checkbox" ? e.target.checked : e.target.value,
    });
  };

  const guardar = async (e) => {
    setIsSaving(true);
    try {
      const { data } = await catalogoService.set({ navigate, catalogo });
      setIsSaving(false);
      const catalogoActualizado = data.listObject[0];
      setCatalogo(catalogoActualizado);
      message.success("Catálogo guardado correctamente");
    } catch (error) {
      setIsSaving(false);
      message.error("No pudimos hacer la solicitud para guardar el catalogo");
    }
  };

  const eliminar = async (e) => {
    setIsDeleting(true);
    try {
      const { data } = await catalogoService.del({ navigate, catalogo });
      setIsDeleting(false);
      if (data === true) {
        message.success("Catálogo eliminado");
        navigate.push("/catalogos");
      } else {
        message.error("No fue posible eliminar el catálogo");
      }
    } catch (error) {
      setIsDeleting(false);
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para eliminar el catálogo"
      );
    }
  };

  const guardarSubCatalogo = async () => {
    setIsSaving(true);
    try {
      const { data } = await catalogoService.set({
        navigate,
        catalogo: { ...subCatalogo },
      });
      setIsSaving(false);
      if (data) {
        setSubCatalogo(data.listObject[0]);
        message.success("Catálogo guardado correctamente");
        setIsLoading(true);
      }
    } catch (error) {
      setIsSaving(false);
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para guardar el catálogo"
      );
    }
  };

  const eliminarSubCatalogo = async () => {
    setIsDeleting(true);
    try {
      const { data } = await catalogoService.del({
        navigate,
        catalogo: subCatalogo,
      });
      setIsDeleting(false);
      if (data === true) {
        setIsLoading(true);
        message.success("Catálogo eliminado correctamente");
        cerrarSubCatalogo();
      } else {
        message.error("No fue posible eliminar el catálogo");
      }
    } catch (error) {
      setIsDeleting(false);
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para eliminar el catálogo"
      );
    }
  };

  const quitarSubCatalogo = async (c) => {
    setIsDeleting(true);
    try {
      const { data } = await catalogoService.del({
        navigate,
        catalogo: { ...c },
      });
      setIsDeleting(false);
      if (data === true) {
        setIsLoading(true);
        message.success("Catálogo eliminado correctamente");
        cerrarSubCatalogo();
      } else {
        message.error("No fue posible eliminar el catálogo");
      }
    } catch (error) {
      setIsDeleting(false);
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para eliminar el catálogo"
      );
    }
  };

  return (
    <>
      <Row align="middle" gutter={10}>
        <Col>
          <Link to={"/catalogos"}>
            <anticons.ArrowLeftOutlined />
          </Link>
        </Col>
        <Col>
          <Typography.Title
            level={4}
            style={{
              margin: 0,
            }}
          >
            {catalogo && catalogo.catNombre}
          </Typography.Title>
        </Col>
      </Row>
      <Divider></Divider>
      {isLoading ? (
        <div className="p-10 text-center">
          <Spin size="large" />
          <br />
          <Typography.Text>Espere un momento...</Typography.Text>
        </div>
      ) : (
        <Form
          layout={"vertical"}
          form={form}
          initialValues={{
            layout: "vertical",
          }}
        >
          <Row gutter={10}>
            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
              <Form.Item label="Nombre">
                <Input
                  id="catNombre"
                  name="catNombre"
                  label="Nombre"
                  type="text"
                  value={catalogo.catNombre}
                  onChange={onChange}
                />
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
              <Form.Item label="Valor">
                <Input
                  id="catValor"
                  name="catValor"
                  label="Valor"
                  type="text"
                  value={catalogo.catValor}
                  onChange={onChange}
                />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={10}>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              <Form.Item label="Descripción">
                <Input
                  id="catDescripcion"
                  name="catDescripcion"
                  label="Descripción"
                  type="text"
                  value={catalogo.catDescripcion}
                  onChange={onChange}
                />
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              <Form.Item label="Estado">
                <Checkbox
                  id="catEstado"
                  name="catEstado"
                  onChange={onChange}
                  checked={catalogo.catEstado}
                >
                  {catalogo.catEstado ? "Habilitado" : "Deshabilitado"}
                </Checkbox>
              </Form.Item>
            </Col>
          </Row>
          <Row justify="end">
            <Col>
              <Button
                type="default"
                onClick={(e) => abrirSubCatalogo(getNuevoSubCatalogo())}
                icon={<anticons.PlusOutlined />}
              >
                Agregar
              </Button>
            </Col>
          </Row>
          <br />
          <Row>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              {catalogos && catalogos.length > 0 && (
                <Table
                  rowKey="catId"
                  columns={columnas}
                  dataSource={catalogos}
                  loading={isLoading}
                  size="small"
                />
              )}
            </Col>
          </Row>
          <Row justify="end">
            <Col>
              <Form.Item>
                <Space>
                  <Button
                    type="primary"
                    onClick={(e) => guardar(e)}
                    disabled={isSaving || isDeleting}
                    loading={isSaving}
                    icon={<anticons.SaveFilled />}
                  >
                    {isSaving ? "Guardando" : "Guardar"}
                  </Button>
                  {catalogos && catalogos.length <= 0 && (
                    <Popconfirm
                      title="¿Seguro(a) que desea eliminar el catálogo?"
                      okText="Si, eliminar"
                      cancelText="No"
                      onConfirm={(e) => eliminar(e)}
                      okButtonProps={{
                        loading: isDeleting,
                      }}
                    >
                      <Button
                        type="danger"
                        disabled={isDeleting || isSaving}
                        loading={isDeleting}
                        icon={<anticons.DeleteFilled />}
                      >
                        {isDeleting ? "Eliminando" : "Eliminar"}
                      </Button>
                    </Popconfirm>
                  )}
                </Space>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      )}
      <Drawer
        title="Catálogo"
        placement="right"
        closable={true}
        onClose={cerrarSubCatalogo}
        open={subCatalogoAbierto}
      >
        <Form
          layout={"vertical"}
          form={formSubCatalogo}
          initialValues={{
            layout: "vertical",
          }}
        >
          <Row gutter={10}>
            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
              <Form.Item label="Nombre">
                <Input
                  id="catNombre"
                  name="catNombre"
                  label="Nombre"
                  type="text"
                  value={subCatalogo.catNombre}
                  onChange={onChangeSubCatalogo}
                />
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
              <Form.Item label="Valor">
                <Input
                  id="catValor"
                  name="catValor"
                  label="Valor"
                  type="text"
                  value={subCatalogo.catValor}
                  onChange={onChangeSubCatalogo}
                />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={10}>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              <Form.Item label="Descripción">
                <Input
                  id="catDescripcion"
                  name="catDescripcion"
                  label="Descripción"
                  type="text"
                  value={subCatalogo.catDescripcion}
                  onChange={onChangeSubCatalogo}
                />
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              <Form.Item label="Estado">
                <Checkbox
                  id="catEstado"
                  name="catEstado"
                  onChange={onChangeSubCatalogo}
                  checked={subCatalogo.catEstado}
                >
                  {subCatalogo.catEstado ? "Habilitado" : "Deshabilitado"}
                </Checkbox>
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Item>
                <Space>
                  <Button
                    type="primary"
                    onClick={(e) => guardarSubCatalogo(e)}
                    disabled={isSaving || isDeleting}
                    loading={isSaving}
                    icon={<anticons.SaveFilled />}
                  >
                    {isSaving ? "Guardando" : "Guardar"}
                  </Button>
                  {subCatalogo && subCatalogo.catId && (
                    <Popconfirm
                      title="¿Seguro(a) que desea eliminar el catálogo?"
                      okText="Si, eliminar"
                      cancelText="No"
                      onConfirm={() => eliminarSubCatalogo()}
                      okButtonProps={{
                        loading: isDeleting,
                      }}
                    >
                      <Button
                        type="danger"
                        disabled={isDeleting || isSaving}
                        loading={isDeleting}
                        icon={<anticons.DeleteFilled />}
                      >
                        {isDeleting ? "Eliminando" : "Eliminar"}
                      </Button>
                    </Popconfirm>
                  )}
                </Space>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Drawer>
    </>
  );
};

export default Catalogo;
