import React, { useState, useEffect } from "react";
import { useHistory, Link } from "react-router-dom";
import {
  Typography,
  Button,
  Space,
  Table,
  Row,
  Col,
  Divider,
  Drawer,
  Form,
  message,
  Input,
  Popconfirm,
  Checkbox,
} from "antd";
import * as anticons from "@ant-design/icons";
import CatalogoService from "../../service/CatalogoService";

const catalogoService = new CatalogoService();

const Catalogos = () => {
  const navigate = useHistory();
  const [load, setLoad] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);
  const [catalogos, setCatalogos] = useState([]);
  const [catalogosSeleccionados, setCatalogosSeleccionados] = useState([]);
  const [catalogo, setCatalogo] = useState({});
  const [catalogoAbierto, setCatalogoAbierto] = useState(false);
  const [formCatalogo] = Form.useForm();

  const tableProps = {
    bordered: false,
    loading: false,
    title: "",
    showHeader: true,
    rowSelection: {},
    size: "medium",
    pagination: {
      position: ["none", "bottomRight"],
    },
  };

  const columnas = [
    {
      title: "Nombre",
      dataIndex: "catDescripcion",
      render: (_, { catId, catNombre, catValor }) => (
        <Link to={`/catalogo/${catId}/${catValor}`}>{catNombre}</Link>
      ),
    },
    {
      title: "Descripción",
      dataIndex: "catDescripcion",
      responsive: ["xl"],
    },
    {
      title: "Estado",
      render: (e) => (e ? "Habilitado" : "Deshabilitado"),
    },
  ];

  const rowSelection = {
    onChange: (selectedRowKeys, selectedRows) => {
      setCatalogosSeleccionados(selectedRows);
    },
  };

  const onChangeCatalogo = (e) => {
    setCatalogo({
      ...catalogo,
      [e.target.name]:
        e.target.type === "checkbox" ? e.target.checked : e.target.value,
    });
  };

  const fetchCatalogos = async () => {
    setLoad(false);
    setIsLoading(true);
    // let filtros = {};
    // if (nombre) {
    //   filtros = { ...filtros, catNombre: nombre };
    // }
    // if (estado !== null) {
    //   filtros = { ...filtros, catEstado: estado };
    // }
    try {
      // const { data } = catalogoService.getsFiltro({ navigate, filtros });
      const { data } = await catalogoService.gets({ navigate });
      if (data) {
        setCatalogos(data.listObject.filter((c) => c !== null));
      } else {
        message.error("Hubo un error al obtener los catálogos");
      }
    } catch (error) {
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para obtener el catálogo"
      );
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    if (load) {
      fetchCatalogos();
    }
  });

  const getNuevoCatalogo = () => ({
    catDescripcion: "",
    catEstado: true,
    catNombre: "",
    catValor: "",
  });

  const abrirCatalogo = (c) => {
    setCatalogo({
      ...c,
    });
    setCatalogoAbierto(true);
  };

  const cerrarCatalogo = () => {
    setCatalogoAbierto(false);
  };

  const guardarCatalogo = async () => {
    setIsSaving(true);
    try {
      const { data } = await catalogoService.set({ navigate, catalogo });
      setIsSaving(false);
      if (data) {
        setCatalogo(data.listObject[0]);
        message.success("Catálogo guardado correctamente");
        setIsLoading(true);
      }
    } catch (error) {
      setIsSaving(false);
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para guardar el catálogo"
      );
    }
  };

  const eliminarCatalogos = (e) => {
    const requests = [];
    catalogosSeleccionados.forEach((c) => {
      requests.push(catalogoService.del({ navigate, catalogo: { ...c } }));
    });
    setIsDeleting(true);
    Promise.all(requests)
      .then((results) => {
        setCatalogosSeleccionados([]);
        setIsDeleting(false);
        setIsLoading(true);
      })
      .catch((error) => {
        setIsDeleting(false);
        const { response } = error;
        const msgError =
          response.status === 409
            ? response.data[0].messageText
            : response.data;
        message.error(`No fue posible eliminar los catálogos: ${msgError}`);
      });
  };

  const eliminarCatalogo = async () => {
    setIsDeleting(true);
    try {
      const { data } = await catalogoService.del({ navigate, catalogo });
      setIsDeleting(false);
      if (data === true) {
        setIsLoading(true);
        message.success("Catálogo eliminado correctamente");
        cerrarCatalogo();
      } else {
        message.error("No fue posible eliminar el catálogo");
      }
    } catch (error) {
      setIsDeleting(false);
      const { response } = error;
      message.error(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para eliminar el catálogo"
      );
    }
  };

  return (
    <>
      <Row gutter={[10, 10]}>
        <Col xs={24} sm={12} md={12} lg={12} xl={12}>
          <Row justify="start">
            <Typography.Title
              level={4}
              style={{
                margin: 0,
              }}
            >
              Gestión de Catálogos
            </Typography.Title>
          </Row>
        </Col>
        <Col xs={24} sm={12} md={12} lg={12} xl={12}>
          <Row justify="end">
            <Space>
              <Button
                type="default"
                onClick={(e) => abrirCatalogo(getNuevoCatalogo())}
                icon={<anticons.PlusOutlined />}
              >
                Nuevo
              </Button>
              {catalogosSeleccionados && catalogosSeleccionados.length > 0 && (
                <Popconfirm
                  title="¿Seguro(a) que desea eliminar los catálogos seleccionados?"
                  okText="Si, eliminar"
                  cancelText="No"
                  onConfirm={() => eliminarCatalogos()}
                  okButtonProps={{
                    loading: isDeleting,
                  }}
                >
                  <Button
                    type="danger"
                    disabled={isDeleting}
                    loading={isDeleting}
                    icon={<anticons.DeleteFilled />}
                  >
                    {isDeleting ? "Eliminando" : "Eliminar"}
                  </Button>
                </Popconfirm>
              )}
              <Button
                type="primary"
                onClick={fetchCatalogos}
                loading={isLoading}
                icon={<anticons.SearchOutlined />}
              >
                Consultar
              </Button>
            </Space>
          </Row>
        </Col>
      </Row>
      <Divider></Divider>
      {/* <Form layout={"vertical"}>
        <Row gutter={10}>
          <Col xs={24} sm={12} md={12} lg={6} xl={6}>
            <Form.Item label="Nombre">
              <Input
                label="Nombre"
                type="text"
                value={nombre}
                onChange={(e) => setNombre(e.target.value)}
              />
            </Form.Item>
          </Col>
          <Col xs={24} sm={12} md={12} lg={6} xl={6}>
            <Form.Item label="Estado">
              <Radio.Group
                onChange={(e) => setEstado(e.target.value)}
                value={estado}
              >
                <Radio value={true}>Activo</Radio>
                <Radio value={false}>Inactivo</Radio>
              </Radio.Group>
            </Form.Item>
          </Col>
        </Row>
      </Form> */}
      <Table
        {...tableProps}
        rowKey="catId"
        rowSelection={{
          type: "checkbox",
          ...rowSelection,
        }}
        columns={columnas}
        dataSource={catalogos}
        loading={isLoading}
      />

      <Drawer
        title="Catálogo"
        placement="right"
        closable={true}
        onClose={cerrarCatalogo}
        open={catalogoAbierto}
      >
        <Form
          layout={"vertical"}
          form={formCatalogo}
          initialValues={{
            layout: "vertical",
          }}
        >
          <Row gutter={10}>
            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
              <Form.Item label="Nombre">
                <Input
                  id="catNombre"
                  name="catNombre"
                  label="Nombre"
                  type="text"
                  value={catalogo.catNombre}
                  onChange={onChangeCatalogo}
                />
              </Form.Item>
            </Col>
            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
              <Form.Item label="Valor">
                <Input
                  id="catValor"
                  name="catValor"
                  label="Valor"
                  type="text"
                  value={catalogo.catValor}
                  onChange={onChangeCatalogo}
                />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={10}>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              <Form.Item label="Descripción">
                <Input
                  id="catDescripcion"
                  name="catDescripcion"
                  label="Descripción"
                  type="text"
                  value={catalogo.catDescripcion}
                  onChange={onChangeCatalogo}
                />
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col xs={24} sm={24} md={24} lg={24} xl={24}>
              <Form.Item label="Estado">
                <Checkbox
                  id="catEstado"
                  name="catEstado"
                  onChange={onChangeCatalogo}
                  checked={catalogo.catEstado}
                >
                  {catalogo.catEstado ? "Habilitado" : "Deshabilitado"}
                </Checkbox>
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col>
              <Form.Item>
                <Space>
                  <Button
                    type="primary"
                    onClick={(e) => guardarCatalogo(e)}
                    disabled={isSaving || isDeleting}
                    loading={isSaving}
                    icon={<anticons.SaveFilled />}
                  >
                    {isSaving ? "Guardando" : "Guardar"}
                  </Button>
                  {catalogo && catalogo.catId && (
                    <Popconfirm
                      title="¿Seguro(a) que desea eliminar el catálogo?"
                      okText="Si, eliminar"
                      cancelText="No"
                      onConfirm={() => eliminarCatalogo()}
                      okButtonProps={{
                        loading: isDeleting,
                      }}
                    >
                      <Button
                        type="danger"
                        disabled={isDeleting || isSaving}
                        loading={isDeleting}
                        icon={<anticons.DeleteFilled />}
                      >
                        {isDeleting ? "Eliminando" : "Eliminar"}
                      </Button>
                    </Popconfirm>
                  )}
                </Space>
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Drawer>
    </>
  );
};

export default Catalogos;
