import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import {
  Button,
  Row,
  Col,
  Spin,
  Alert,
  Typography,
  Badge,
  Card,
  Image,
  Avatar,
  Statistic,
  Progress,
  List,
  Result,
} from "antd";
import * as anticons from "@ant-design/icons";
import {
  formatoMoneda,
  getEstiloFerificada,
  getResponsable,
  getURLImagenPYMERandom,
  getURLImagenRandom,
} from "./../../utils/Utils";

import InversionService from "../../service/InversionService";
const inversionService = new InversionService();

const { Meta } = Card;

const MisInversiones = () => {
  const navigate = useHistory();
  const [inversiones, setInversiones] = useState([]);
  const [load, setLoad] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);

  const OpenInversion = (c) => {
    navigate.push(`/inversion/${c}`);
  };

  const renderInversion = (i) => {
    const {
      ivsCuotaMensual,
      ivsId,
      ivsIdEstado,
      ivsIdOfertaInv,
      ivsIdPersona,
      ivsMonto,
      ivsPlazo,
      ivsRecaudado,
      ivsTasaTir,
    } = i;
    return (
      <Col key={ivsId} xs={24} sm={12} md={12} lg={8} xl={6}>
        <Badge.Ribbon
          text={
            <span {...getEstiloFerificada()}>
              Verificada{" "}
              <anticons.CheckCircleOutlined></anticons.CheckCircleOutlined>
            </span>
          }
          color="#8A05BE"
        >
          <Card
            bodyStyle={{ background: "#fff" }}
            hoverable={true}
            cover={
              <Image
                src={getURLImagenPYMERandom(ivsId)}
                alt=""
                width={"100%"}
                heigh={"100%"}
                preview={false}
              />
            }
            actions={[]}
          >
            <Meta
              avatar={<Avatar src={getURLImagenRandom(ivsId)} />}
              title={`PYME ${ivsIdPersona}`}
              description={`Oferta de Inversión ${ivsIdOfertaInv}`}
              style={{ paddingBottom: "15px" }}
            />
            {/* <Row>
              <Col span={24}>
                <Typography.Title level={5}>
                  Datos Inversión {oinIdPersona}
                </Typography.Title>
                <Typography.Text>
                  <p>
                    {oinDetalle}
                  </p>
                </Typography.Text>
              </Col>
            </Row> */}
            <Row>
              <Col span={12}>
                <Statistic
                  title={"Recaudado"}
                  value={
                    ivsRecaudado && ivsMonto
                      ? (((ivsRecaudado || 0) * 100) / (ivsMonto || 0)).toFixed(
                          2
                        )
                      : 0
                  }
                  valueStyle={{
                    color: "#000",
                    fontSize: "1em",
                  }}
                  suffix="%"
                />
              </Col>
              <Col span={12}>
                <Row justify="end">
                  <Statistic
                    title={"Objetivo"}
                    value={formatoMoneda(ivsMonto || 0)}
                    valueStyle={{
                      color: "#000",
                      fontSize: "1em",
                    }}
                  />
                </Row>
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <Progress
                  strokeLinecap="square"
                  strokeColor={{
                    "0%": "#8A05BE",
                    "100%": "#006e2c",
                  }}
                  percent={(
                    ((ivsRecaudado || 0) * 100) /
                    (ivsMonto || 0)
                  ).toFixed(2)}
                  showInfo={false}
                  trailColor={"#ccc"}
                  size="small"
                />
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <List
                  bordered={false}
                  dataSource={[
                    {
                      label: "Estado",
                      value: ivsIdEstado,
                    },
                    {
                      label: "Inversión",
                      value: formatoMoneda(ivsMonto),
                    },
                    {
                      label: "TIR",
                      value: `${ivsTasaTir}%`,
                    },
                    {
                      label: "Cuota",
                      value: formatoMoneda(ivsCuotaMensual),
                    },
                    {
                      label: "Plazo",
                      value: `${ivsPlazo} meses`,
                    },
                  ]}
                  renderItem={(i) => (
                    <List.Item
                      extra={<Typography.Text>{i.value}</Typography.Text>}
                    >
                      <Typography.Text style={{ fontWeight: 600 }}>
                        {i.label}
                      </Typography.Text>
                    </List.Item>
                  )}
                ></List>
              </Col>
            </Row>
            {/* <Row style={{ textAlign: 'center', paddingTop: '15px' }}>
              <Col span={8} style={{ borderRight: 'solid 1px #f0f0f0' }}>
                <Tooltip title="Plazo">
                  <div>
                    <anticons.CalendarOutlined />
                    <br />
                    {ivsPlazo} Meses
                  </div>
                </Tooltip>
              </Col>
              <Col span={8} style={{ borderRight: 'solid 1px #f0f0f0' }}>
                <Tooltip title="TIR">
                  <div>
                    <anticons.PercentageOutlined />
                    <br />
                    {ivsTasaTir}
                  </div>
                </Tooltip>
              </Col>
              <Col span={8}>
                <Tooltip title="Estado">
                  <div>
                    <anticons.UserOutlined />
                    <br />
                    {ivsIdEstado}
                  </div>
                </Tooltip>
              </Col>
            </Row> */}
            <Row style={{ textAlign: "center", paddingTop: "15px" }}>
              <Col span={24}>
                <Button type="primary" onClick={() => OpenInversion(i.ivsId)}>
                  Ver más
                </Button>
              </Col>
            </Row>
          </Card>
        </Badge.Ribbon>
      </Col>
    );
  };

  const fetchData = async () => {
    setLoad(false);
    setIsLoading(true);
    try {
      const { data } = await inversionService.getInversionesPorInversionista({
        navigate,
        inversionista: { ivsIdInversionista: getResponsable() },
      });

      if (data) {
        setInversiones(data.listObject);
      } else {
        setError("Hubo un error al obtener las ofertas de invesión");
      }
    } catch (error) {
      const { response } = error;
      setError(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para obtener las ofertas de inversión"
      );
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    if (load) fetchData();
  });

  return (
    <>
      {isLoading && (
        <Row>
          <Col span={24}>
            <Row justify="center">
              <Spin size="large" />
            </Row>
          </Col>
        </Row>
      )}
      {error && (
        <Row>
          <Col span={24} style={{ padding: "0 0 15px 0" }}>
            <Alert description={error} type="warning" showIcon banner></Alert>
          </Col>
        </Row>
      )}
      <Row>
        <Col span={24}>
          <Typography.Paragraph>
            <Typography.Title level={4} style={{ margin: 0 }}>
              Mis Inversiones
            </Typography.Title>
          </Typography.Paragraph>
        </Col>
      </Row>
      <Row justify="center" gutter={[20, 30]}>
        {inversiones.length > 0
          ? inversiones.map((i) => renderInversion(i))
          : !isLoading && (
              <Result
                icon={<Image src="./assets/Agente_6.png" preview={false} />}
                extra={
                  <>
                    <Alert
                      message={
                        <Typography.Title
                          level={5}
                          style={{ textAlign: "left" }}
                        >
                          No tienes inversiones activas
                        </Typography.Title>
                      }
                      description={
                        <>
                          <Typography.Paragraph style={{ textAlign: "left" }}>
                            Existe una variedad de empresas en las que puedes
                            invertir en forma de crédito directo. No esperes
                            más, realiza tu primera inversión.
                          </Typography.Paragraph>
                          <Typography.Paragraph>
                            <Button
                              type="primary"
                              style={{ letterSpacing: "1.5px" }}
                              onClick={() => {
                                navigate.push("/");
                              }}
                            >
                              Quiero Invertir
                            </Button>
                          </Typography.Paragraph>
                        </>
                      }
                      type="info"
                      showIcon
                    />
                  </>
                }
              />
            )}
      </Row>
    </>
  );
};

export default MisInversiones;
