import React, { useState, useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";
import {
  Button,
  Row,
  Col,
  Spin,
  Alert,
  Typography,
  Badge,
  Card,
  Image,
  Avatar,
  List,
  Result,
  Space,
} from "antd";
import * as anticons from "@ant-design/icons";
import {
  formatoMoneda,
  getEstiloFerificada,
  getURLImagenRandom,
} from "./../../utils/Utils";

import SolicitudInversionService from "../../service/SolicitudInversionService";
const solicitudInversionService = new SolicitudInversionService();

const { Meta } = Card;

const Solicitudes = () => {
  const navigate = useHistory();
  const { filtro } = useParams();
  const [inversiones, setInversiones] = useState([]);
  const [load, setLoad] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);

  const openInversionSolicitud = (c) => {
    navigate.push(`/inversion/solicitud/${c}`);
  };

  const renderSolicitud = (i) => {
    const { invId, oinId, slcCuotaMensual, slcEstado, slcId, slcMonto } = i;
    return (
      <Col key={slcId} xs={24} sm={12} md={12} lg={8} xl={6}>
        <Badge.Ribbon
          text={
            <span {...getEstiloFerificada()}>
              Inversión Verificada{" "}
              <anticons.CheckCircleOutlined></anticons.CheckCircleOutlined>
            </span>
          }
        >
          <Card
            bodyStyle={{ background: "#fff" }}
            hoverable={true}
            cover={
              <>
                <br />
              </>
            }
            actions={[]}
          >
            <Meta
              avatar={<Avatar src={getURLImagenRandom(slcId)} />}
              title={`${invId.usuId.nombres} ${invId.usuId.apellidos}`}
              description={`Oferta de Inversión para ${oinId.oinDetalle}`}
              style={{ paddingBottom: "15px" }}
            />
            {/* <Row>
                <Col span={24}>
                  <Typography.Title level={5}>
                    Datos Inversión {oinIdPersona}
                  </Typography.Title>
                  <Typography.Text>
                    <p>
                      {oinDetalle}
                    </p>
                  </Typography.Text>
                </Col>
              </Row> */}
            {/* <Row>
                            <Col span={12}>
                                <Statistic
                                    title={
                                        'Recaudado'
                                    }
                                    value={
                                        ivsRecaudado && ivsMonto ?
                                            ((ivsRecaudado || 0) * 100 / (ivsMonto || 0)).toFixed(2) : 0
                                    }
                                    valueStyle={{
                                        color: '#000',
                                        fontSize: '1em'
                                    }}
                                    suffix="%"
                                />
                            </Col>
                            <Col span={12}>
                                <Row justify='end'>
                                    <Statistic
                                        title={
                                            'Objetivo'
                                        }
                                        value={formatoMoneda(ivsMonto || 0)}
                                        valueStyle={{
                                            color: '#000',
                                            fontSize: '1em'
                                        }}
                                    />
                                </Row>
                            </Col>
                        </Row> */}
            {/* <Row>
                            <Col span={24}>
                                <Progress
                                    strokeLinecap='square'
                                    strokeColor={{
                                        '0%': '#8A05BE',
                                        '100%': '#006e2c',
                                    }}
                                    percent={((ivsRecaudado || 0) * 100 / (ivsMonto || 0)).toFixed(2)}
                                    showInfo={false} trailColor={'#ccc'} size='small' />
                            </Col>
                        </Row> */}
            <Row>
              <Col span={24}>
                <List
                  bordered={false}
                  dataSource={[
                    {
                      label: "Estado",
                      value: slcEstado.catNombre,
                    },
                    {
                      label: "Inversión",
                      value: formatoMoneda(slcMonto),
                    },
                    {
                      label: "TIR",
                      value: `${oinId.oinTir}%`,
                    },
                    {
                      label: "Cuota",
                      value: formatoMoneda(slcCuotaMensual),
                    },
                    {
                      label: "Plazo",
                      value: `${oinId.oinPlazo} ${oinId.oinTipoPlazo.catNombre}`,
                    },
                  ]}
                  renderItem={(i) => (
                    <List.Item
                      extra={<Typography.Text>{i.value}</Typography.Text>}
                    >
                      <Typography.Text style={{ fontWeight: 600 }}>
                        {i.label}
                      </Typography.Text>
                    </List.Item>
                  )}
                ></List>
              </Col>
            </Row>
            {/* <Row style={{ textAlign: 'center', paddingTop: '15px' }}>
                <Col span={8} style={{ borderRight: 'solid 1px #f0f0f0' }}>
                  <Tooltip title="Plazo">
                    <div>
                      <anticons.CalendarOutlined />
                      <br />
                      {ivsPlazo} Meses
                    </div>
                  </Tooltip>
                </Col>
                <Col span={8} style={{ borderRight: 'solid 1px #f0f0f0' }}>
                  <Tooltip title="TIR">
                    <div>
                      <anticons.PercentageOutlined />
                      <br />
                      {ivsTasaTir}
                    </div>
                  </Tooltip>
                </Col>
                <Col span={8}>
                  <Tooltip title="Estado">
                    <div>
                      <anticons.UserOutlined />
                      <br />
                      {ivsIdEstado}
                    </div>
                  </Tooltip>
                </Col>
              </Row> */}
            <Row style={{ textAlign: "center", paddingTop: "15px" }}>
              <Col span={24}>
                <Space direction="horizontal">
                  <Button
                    type="primary"
                    onClick={() => openInversionSolicitud(i.slcId)}
                  >
                    Ver más
                  </Button>
                </Space>
              </Col>
            </Row>
          </Card>
        </Badge.Ribbon>
      </Col>
    );
  };

  // const fetchEstados = async () => {
  //   setLoad(false);
  //   setIsLoading(true);
  //   try {
  //     const { data } = await catalogoService.getEstadosSolicitud();
  //     setEstadosSolicitud(data.listObject);
  //     fetchData();
  //   } catch (error) {
  //     const { response } = error;
  //     setError(
  //       response.status === 409
  //         ? response.data[0].messageText
  //         : "No pudimos hacer la solicitud para obtener los estados"
  //     );
  //     setLoad(false);
  //     setIsLoading(false);
  //   }
  // };

  const fetchData = async () => {
    setLoad(false);
    setIsLoading(true);
    const solicitudInversion = {
      slcIdEstado: filtro === "revision" ? 93 : 0,
    };
    try {
      const { data } = await solicitudInversionService.getsFilter({
        navigate,
        solicitudInversion,
      });
      if (data) {
        setInversiones(data.listObject);
      } else {
        setError("Hubo un error al obtener las ofertas de invesión");
      }
    } catch (error) {
      const { response } = error;
      setError(
        response.status === 409
          ? response.data[0].messageText
          : "No pudimos hacer la solicitud para obtener las solicitudes"
      );
    } finally {
      setIsLoading(false);
    }
  };

  // const renderEstado = ({ catId, catValor, catNombre }) => (
  //   <Select.Option key={catId} value={catValor} text={catNombre}>
  //     {catNombre}
  //   </Select.Option>
  // );

  useEffect(() => {
    if (load) fetchData();
  });

  return (
    <>
      {isLoading && (
        <Row>
          <Col span={24}>
            <Row justify="center">
              <Spin size="large" />
            </Row>
          </Col>
        </Row>
      )}
      {error && (
        <Row>
          <Col span={24} style={{ padding: "0 0 15px 0" }}>
            <Alert description={error} type="warning" showIcon banner></Alert>
          </Col>
        </Row>
      )}
      <Row>
        <Col span={24}>
          <Typography.Paragraph>
            <Typography.Title level={4} style={{ margin: 0 }}>
              Solicitudes de Inversión
            </Typography.Title>
          </Typography.Paragraph>
        </Col>
        {/* <Col span={24}>
                    <Select
                        mode="multiple"
                        allowClear
                        style={{
                            width: '100%',
                        }}
                        placeholder="Please select"
                    >
                        {
                            estadosSolicitud && estadosSolicitud.map(e => renderEstado(e))
                        }
                    </Select>
                </Col> */}
      </Row>
      <Row justify="center" gutter={[20, 30]}>
        {inversiones.length > 0
          ? inversiones.map((i) => renderSolicitud(i))
          : !isLoading && (
              <Result
                icon={<Image src="./assets/Agente_6.png" preview={false} />}
                extra={
                  <>
                    <Alert
                      message={
                        <Typography.Title
                          level={5}
                          style={{ textAlign: "left" }}
                        >
                          No tienes solicitudes de inversión activas
                        </Typography.Title>
                      }
                      description={
                        <>
                          <Typography.Paragraph style={{ textAlign: "left" }}>
                            Existe una variedad de empresas en las que puedes
                            invertir en forma de crédito directo. No esperes
                            más, realiza tu primera inversión.
                          </Typography.Paragraph>
                          <Typography.Paragraph>
                            <Button
                              type="primary"
                              style={{ letterSpacing: "1.5px" }}
                              onClick={() => {
                                navigate.push("/");
                              }}
                            >
                              Quiero Invertir
                            </Button>
                          </Typography.Paragraph>
                        </>
                      }
                      type="info"
                      showIcon
                    />
                  </>
                }
              />
            )}
      </Row>
    </>
  );
};

export default Solicitudes;
